import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');
import Guid from 'guid';

var g_cache = {
  down: null,
};
class ListItem extends React.Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.guid = Guid.v4();
    this.onRowMouseDown = this.onRowMouseDown.bind(this);
    this.onRowMouseUp = this.onRowMouseUp.bind(this);
  }

  handleChange() {
    this.props.onToggleSelection(this);
    //this.props.onItemUpdate(this.props.unique, this.refs.checkbox.checked);
    // this.props.item.checked = this.refs.checkbox.checked;
    // this.props.onMultiSelection(this);
  }

  onRowMouseDown(e) {
    g_cache.down = this;

    if (this.props.item.checked) {
      if (e.ctrlKey) {
        this.props.onToggleSelection(this);
      }
      else if (e.shiftKey) {
        this.props.onRangeSelection(this);
      }
    }
    else {
      if (e.ctrlKey) {
        this.props.onToggleSelection(this);
      }
      else if (e.shiftKey) {
        this.props.onRangeSelection(this);
      }
      else {
        this.props.onSingleSelection(this);
      }
    }
  }

  onRowMouseUp(e) {
    if (g_cache.down !== this) return;

    if (this.props.item.checked) {
      if (!e.ctrlKey && !e.shiftKey) {
        this.props.onSingleSelection(this);
      }
    }
    else {

    }
  }

  render() {
    var tdataStyle = {
      textAlign: 'left',
    };

    var visualAttrs = {};
    if (this.props.item.checked) {
      visualAttrs['data-selected'] = '';
    }

    return (
      <tr className="tbl_row no_selection"
          data-unique={this.props.item.unique}
          {...visualAttrs}>
        <th className="tbl_head" unselectable="on">
          <input id={this.guid}
                 type="checkbox"
                 ref="checkbox"
                 onClick={this.handleChange}
                 checked={this.props.item.checked} />
          <label htmlFor={this.guid}></label>
        </th>
        <td className="tbl_data"
            onMouseDown={this.onRowMouseDown}
            onMouseUp={this.onRowMouseUp}
            style={tdataStyle}
            unselectable="on">{this.props.item.text}</td>
      </tr>
    );
  }
}

export default ListItem;



