class TreeNode {
  constructor() {
    this.parentNode = null;
    this.childNode = null;
    this.prevNode = null;
    this.nextNode = null;
    this.depth = 1;
    this.data = null;
    this.hasChild = false;
    this.expanded = false;
    this.unique = null;
    this.text = '';
    this.selected = false;
    this.iconClass = 'ico';
  }

  setNext(node) {
    if (this.nextNode !== null) {
      this.nextNode.prevNode = node;
      node.nextNode = this.nextNode;
    }

    this.nextNode = node;
    node.prevNode = this;

    node.depth = this.depth;

    return this;
  }

  setPrev(node) {
    if (this.prevNode !== null) {
      this.prevNode.nextNode = node;
      node.prevNode = this.prevNode;
    }

    this.prevNode = node;
    this.nextNode = this;

    node.depth = this.depth;

    return this;
  }

  setParent(node) {
    if (this.childNode !== null) {
      //TODO: log
      return this;
    }

    this.childNode = node;
    node.parentNode = this;

    node.depth = this.depth - 1;

    return this;
  }

  setChild(node) {
    if (this.childNode !== null) {
      //TODO: log
      return this;
    }

    this.childNode = node;
    node.parentNode = this;

    node.depth = this.depth + 1;

    return this;
  }

  addSibling(node) {
    var last = this.getLastSibling();
    last.setNext(node);

    return this;
  }

  addChild(node) {
    if (this.childNode === null) {
      this.setChild(node);
    }
    else {
      this.childNode.addSibling(node);
    }

    return this;
  }

  getFirstSibling() {
    var node = this;
    while (node.prevNode !== null) {
      node = node.prevNode;
    }
    return node;
  }

  getLastSibling() {
    var node = this;
    while (node.nextNode !== null) {
      node = node.nextNode;
    }
    return node;
  }

  findParent() {
    if (this.parentNode !== null) return this.parentNode;
    var node = this.prevNode;
    while (node !== null) {
      if (node.parentNode !== null) return node.parentNode;
      node = node.prevNode;
    }
    return null;
  }

  getNthParent(n) {
    var parent = this;
    var node = this;
    while (n > 0) {
      parent = node.findParent();
      node = parent;

      n = n - 1;
    }
    return parent;
  }

}

export default TreeNode;



