import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');
import GridColumn from './grid-column.jsx';
import Guid from 'guid';

var g_cache = {
  down: null,
  latest: null,
  preventToggle: false,
  pressed: false,
  timestamp: null
};
class GridRow extends React.Component {
  constructor(props) {
    super(props);
    this.checkboxId = Guid.v4();
    this.handleChange = this.handleChange.bind(this);
    this.onRowMouseDown = this.onRowMouseDown.bind(this);
    this.onRowMouseUp = this.onRowMouseUp.bind(this);
    this.onRowMouseMove = this.onRowMouseMove.bind(this);
  }

  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }

  handleChange(e) {
    // this.props.item.checked = this.refs.selectCheckbox.checked;
    //this.props.item.checked = !this.props.item.checked;
    // this.props.onCheckChanged(this);
    //this.props.onRowSelectChanged(this);
    this.props.onToggleSelection(this);
  }

  onRowMouseDown(e) {
    g_cache.down = this;

    // single selection
    if (this.props.enableOneClickToggle) {
      if (this.props.item.checked) {
        if (e.ctrlKey) {
          this.props.onToggleSelection(this);
        }
        else if (e.shiftKey) {
          this.props.onRangeSelection(this);
        }
        else {
          
        }
      }
      else {
        if (e.ctrlKey) {
          this.props.onToggleSelection(this);
        }
        else if (e.shiftKey) {
          this.props.onRangeSelection(this);
        }
        else {
          this.props.onToggleSelection(this);
          g_cache.latest = this;
        }
      }

      g_cache.pressed = true;
    }
    // multi selection
    else {
      if (this.props.item.checked) {
        if (e.ctrlKey) {
          this.props.onToggleSelection(this);
        }
        else if (e.shiftKey) {
          this.props.onRangeSelection(this);
        }
      }
      else {
        if (e.ctrlKey) {
          this.props.onToggleSelection(this);
        }
        else if (e.shiftKey) {
          this.props.onRangeSelection(this);
        }
        else {
          this.props.onSingleSelection(this);
        }
      }
    }
  }

  onRowMouseUp(e) {
    // single selection
    if (this.props.enableOneClickToggle) {
      var nowDate = new Date();
      var now = nowDate.getMilliseconds();
      now += nowDate.getSeconds() * 1000;
      //var now = new Date();
      var isDbl = false;
      if (this.props.item.checked) {
        if (e.ctrlKey) {

        }
        else if (e.shiftKey) {

        }
        else {
          if (g_cache.latest === this) {

          }
          else {
            if (g_cache.preventToggle !== true && g_cache.down == this) {
              if (g_cache.timestamp !== null && (now - g_cache.timestamp) <= 300) {
                isDbl = true;
              }
              else {
                this.props.onToggleSelection(this);
                isDbl = false;
              }
            }
          }
        }
      }
      else {
        //now.setSeconds(now.getSeconds() - 1);
        now -= 300;
      }

      g_cache.latest = null;
      g_cache.preventToggle = false;
      g_cache.pressed = false;

      if (isDbl) {
        //g_cache.timestamp = now.setSeconds(now.getSeconds() - 1);
        now -= 300;
        g_cache.timestamp = now;
      }
      else {
        g_cache.timestamp = now;
      }
    }
  }

  onRowMouseMove(e) {
    if (g_cache.latest === null && g_cache.pressed === true) {
      g_cache.preventToggle = true;
    }
  }

  renderColumns() {
    var columns = [];
    if (this.props.item !== null) {
      //for (var cell of this.props.item.cells) {
      for (var i = 0; i < this.props.item.cells.length; i++) {
        var cell = this.props.item.cells[i];
        if (cell.html !== null) {
          columns.push(
            <td key={cell.unique}
                className="tbl_data no_selection"
                onMouseDown={this.onRowMouseDown}
                onMouseUp={this.onRowMouseUp}
                onMouseMove={this.onRowMouseMove}
                dangerouslySetInnerHTML={{__html: cell.html}}
                unselectable="on"></td>
          );
        }
        else {
          var icon = '';
          if (cell.useIcon === true) {
            icon = <span className={cell.iconClass}></span>;
          }
          columns.push(
            <td key={cell.unique}
                className="tbl_data no_selection"
                onMouseDown={this.onRowMouseDown}
                onMouseUp={this.onRowMouseUp}
                onMouseMove={this.onRowMouseMove}
                unselectable="on">{icon}{cell.text}</td>
          );
        }
      }
    }
    return columns
  }

  render() {
    var visualAttrs = {};
    if (this.props.item.checked) {
      visualAttrs['data-selected'] = '';
    }

    return (
      <tr className="tbl_row no_selection"
          data-unique={this.props.item.unique}
          {...visualAttrs}
          >
        <th className="tbl_head no_ellipsis" unselectable="on">
          <input type="checkbox"
                 id={this.checkboxId}
                 ref="selectCheckbox"
                 onClick={this.handleChange}
                 checked={this.props.item.checked}
                 />
          <label htmlFor={this.checkboxId}>
            <span className="checkbox"></span>
          </label>
        </th>
        {this.renderColumns()}
      </tr>
    );
  }
}

export default GridRow;



