import GridItemCell from './grid-item-cell';

class GridItem {
  constructor() {
    this.unique = null;
    this.data = null;
    this.selected = false;
    this.checked = false;
    this.cells = [];
  }

  findCellByName(name) {
    //for (var cell of this.cells) {
    for (var i = 0; i < this.cells.length; i++) {
      var cell = this.cells[i];
      if (cell.columnName === name) {
        return cell;
      }
    }
    return null;
  }

  addCell(cell) {
    this.cells.push(cell);
  }
}

export default GridItem;



