import AppManager from './app-manager';
import LangHelper from './lang-helper';
import DeptSearch from './dept-search';

var $ = require('jquery');
var _ = require('lodash');

var dataTooltip = document.querySelector('#tooltip-data');
var dataTooltipText = document.querySelector('#tooltip-data-txt');
var context = {
  item: null,
  timer: null,
  clientX: 0,
  clientY: 0,
};
var enabled = true;

// tooltip mouse position tracker
document.addEventListener('mousemove', function(e) {
  var deltaX = Math.abs(context.clientX - e.clientX);
  var deltaY = Math.abs(context.clientY - e.clientY);
  context.clientX = e.clientX;
  context.clientY = e.clientY;
  if (deltaX + deltaY > 4) {
    hideTooltip();
  }
}, false);

function showTooltip(x, y, item, margin) {
  if (enabled === false) return;

  hideTooltip();

  var text = '';
  var data = _.get(item, 'data', item);

  text += `<b>${data.DisplayName}</b><br/>`;
  if (_.has(data, 'CompanyName') && data.CompanyName !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.company')}: ${data.CompanyName}<br/>`;
  if (_.has(data, 'EmpID') && data.EmpID !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.empId')}: ${data.EmpID}<br/>`;
  if (_.has(data, 'DeptName') && data.DeptName !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.dept')}: ${data.DeptName}<br/>`;
  if (_.has(data, 'TitleName') && data.TitleName !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.title')}: ${data.TitleName}<br/>`;
  if (_.has(data, 'DutyName') && data.DutyName !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.duty')}: ${data.DutyName}<br/>`;
  if (_.has(data, 'JobName') && data.JobName !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.job')}: ${data.JobName}<br/>`;
  if (_.has(data, 'RankName') && data.RankName !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.rank')}: ${data.RankName}<br/>`;
  if (_.has(data, 'EmailAddress') && data.EmailAddress !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.email')}: ${data.EmailAddress}<br/>`;
  if (_.has(data, 'MobileTel') && data.MobileTel !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.mobileTel')}: ${data.MobileTel}<br/>`;
  if (_.has(data, 'OfficeTel') && data.OfficeTel !== '')
    text += `${LangHelper.get(AppManager.language, 'tooltip.info.officeTel')}: ${data.OfficeTel}<br/>`;

  dataTooltipText.innerHTML = text;
  dataTooltip.style.left = `${x + margin}px`;
  dataTooltip.style.top = `${y + margin}px`;
  dataTooltip.style.display = 'block';

  // top fix
  if (dataTooltip.offsetTop + dataTooltip.clientHeight > window.innerHeight) {
    dataTooltip.style.top = `${y - dataTooltip.clientHeight - margin}px`;
  }
  // right fix
  if (dataTooltip.offsetLeft + dataTooltip.clientWidth > window.innerWidth) {
    var rightOver = window.innerWidth - (dataTooltip.offsetLeft + dataTooltip.clientWidth);
    var leftOver = dataTooltip.offsetLeft - dataTooltip.clientWidth;

    if (leftOver > rightOver) {
      dataTooltip.style.left = `${x - dataTooltip.clientWidth - margin}px`;
    }
  }
};

function hideTooltip() {
  dataTooltip.style.display = 'none';
}

function resetTooltip() {
  context.item = null;
  if (context.timer !== null) {
    clearTimeout(context.timer);
  }
  hideTooltip();
}

function tooltipTimer(item, x, y) {
  if (enabled === false) return;

  context.item = item;
  context.timer = setTimeout(() => {
    if (context.item !== null) {
      if (typeof x !== typeof undefined && typeof y !== typeof undefined) {
        showTooltip(x, y, context.item, 0);
      }
      else {
        showTooltip(context.clientX, context.clientY, context.item, 20);
      }
    }
  }, 600);
}

function blockTooltip() {
  enabled = false;
}
function releaseTooltip() {
  enabled = true;
}

export { tooltipTimer, resetTooltip, blockTooltip, releaseTooltip };

export default {
  initialize: function() {
    $('#orgTree')
    .on('mouseover', 'a', function(e) {
      var item = AppManager.controls.tree.getMouseOverItem();
      if (item === null) {
        return;
      }
      tooltipTimer(item);
    })
    .on('mouseout', 'a', function(e) {
      context.item = null;
      if (context.timer !== null) {
        clearTimeout(context.timer);
      }
      hideTooltip();
    });

    $('#orgGrid')
    .on('mouseover', 'tr', function(e) {
      var unique = this.getAttribute('data-unique');
      if (typeof unique === typeof undefined || unique === null) {
        return;
      }

      var item = AppManager.controls.grid.getItemByUnique(unique);
      tooltipTimer(item);
    })
    .on('mouseout', 'tr', function(e) {
      context.item = null;
      if (context.timer !== null) {
        clearTimeout(context.timer);
      }
      hideTooltip();
    });

    $('#orgSelectList')
    .on('mouseover', 'tr', function(e) {
      var unique = this.getAttribute('data-unique');
      if (typeof unique === typeof undefined || unique === null) {
        return;
      }

      var item = AppManager.controls.selectList.getItemByUnique(unique);
      tooltipTimer(item);
    })
    .on('mouseout', 'tr', function(e) {
      context.item = null;
      if (context.timer !== null) {
        clearTimeout(context.timer);
      }
      hideTooltip();
    });

    $('#orgToList')
    .on('mouseover', 'tr', function(e) {
      var unique = this.getAttribute('data-unique');
      if (typeof unique === typeof undefined || unique === null) {
        return;
      }

      var item = AppManager.controls.to.getItemByUnique(unique);
      tooltipTimer(item);
    })
    .on('mouseout', 'tr', function(e) {
      context.item = null;
      if (context.timer !== null) {
        clearTimeout(context.timer);
      }
      hideTooltip();
    });

    $('#orgCcList')
    .on('mouseover', 'tr', function(e) {
      var unique = this.getAttribute('data-unique');
      if (typeof unique === typeof undefined || unique === null) {
        return;
      }

      var item = AppManager.controls.cc.getItemByUnique(unique);
      tooltipTimer(item);
    })
    .on('mouseout', 'tr', function(e) {
      context.item = null;
      if (context.timer !== null) {
        clearTimeout(context.timer);
      }
      hideTooltip();
    });

    $('#orgBccList')
    .on('mouseover', 'tr', function(e) {
      var unique = this.getAttribute('data-unique');
      if (typeof unique === typeof undefined || unique === null) {
        return;
      }

      var item = AppManager.controls.bcc.getItemByUnique(unique);
      tooltipTimer(item);
    })
    .on('mouseout', 'tr', function(e) {
      context.item = null;
      if (context.timer !== null) {
        clearTimeout(context.timer);
      }
      hideTooltip();
    });

    $('#select-dept-search')
    .on('mouseover', 'li', function(e) {
      var code = this.getAttribute('data-code');
      if (typeof code === typeof undefined || code === null) {
        return;
      }

      var item = DeptSearch.getItem(code);
      var offset = $(e.target).offset();
      var x = offset.left + e.target.clientWidth + 2;
      var y = offset.top;
      tooltipTimer(item, x, y);
    })
    .on('mouseout', 'li', function(e) {
      context.item = null;
      if (context.timer !== null) {
        clearTimeout(context.timer);
      }
      hideTooltip();
    });
  }
}



