var _ = require('lodash');

class OrgConfig {
  constructor() {
    this.section = 'APP';
    this.appType = 'DEPTUSER';
    this.onlyOneSelect = false;
    this.callback = null;
    this.orgMapData = null;
    this.useNameCard = true;
    this.isSelectCompany = true;
    this.isSelectChildCompany = true;
    this.returnType = 'JSON';
    this.onlyUser = false;
    this.showToolbar = true;
    this.selectDeptCode = '';
    this.oneClickToggle = false;

    this.deptSelection = true;

    this.title = {
      ko: '',
      en: 'Organization Chart',
      zh: '',
    }
  }

  initialize() {
    // get section from server rendering
    if (_.has(window.AppConfig, 'section')) {
      this.section = window.AppConfig.section.toUpperCase();
    }

    if (window.parent && _.has(window.parent, 'g_orgLoadConfig')) {
      this.parseConfigParam(window.parent.g_orgLoadConfig);
    }
    else if (window.top.opener && _.has(window.top.opener, 'g_orgLoadConfig')) {
      this.parseConfigParam(window.top.opener.g_orgLoadConfig);
    }
    else if (window.dialogArguments) {
      this.parseConfigParam(window.dialogArguments);
    }

    var queryString = this.getQueryString();
    this.parseConfigParam(queryString);

    this.updateTitle();
  }

  updateTitle() {
    if (window.AppConfig) {
      this.parseTitleParam(window.AppConfig);
    }

    if (window.parent && _.has(window.parent, 'g_orgLoadConfig')) {
      this.parseTitleParam(window.parent.g_orgLoadConfig);
    }
    else if (window.top.opener && _.has(window.top.opener, 'g_orgLoadConfig')) {
      this.parseTitleParam(window.top.opener.g_orgLoadConfig);
    }
    else if (window.dialogArguments) {
      this.parseTitleParam(window.dialogArguments);
    }

    var queryString = this.getQueryString();
    this.parseTitleParam(queryString);
  }

  getQueryString() {
    var query_string = {};
    var query = window.location.search.substring(1);
    var vars = query.split("&");
    for (var i=0;i<vars.length;i++) {
      var pair = vars[i].split("=");
      if (typeof query_string[pair[0]] === "undefined") {
        query_string[pair[0]] = decodeURIComponent(pair[1]);
      } else if (typeof query_string[pair[0]] === "string") {
        var arr = [query_string[pair[0]], decodeURIComponent(pair[1])];
        query_string[pair[0]] = arr;
      } else {
        query_string[pair[0]].push(decodeURIComponent(pair[1]));
      }
    }
    return query_string;
  }

  parseTitleParam(o) {
    if (_.has(o, 'title')) {
      var title = o['title'];
      if (typeof title === 'string') {
        this.title.ko = title;
        this.title.en = title;
        this.title.zh = title;
      }
      else {
        if (_.has(title, 'ko')) {
          this.title.ko = title.ko;
        }
        if (_.has(title, 'en')) {
          this.title.en = title.en;
        }
        if (_.has(title, 'zh')) {
          this.title.zh = title.zh;
        }
      }
    }
  }

  parseConfigParam(o) {
    if (_.has(o, 'AppType')) {
      this.appType = o['AppType'].toUpperCase();
    }
    else if (_.has(o, 'appType')) {
      this.appType = o['appType'].toUpperCase();
    }

    if (_.has(o, 'UseNameCard')) {
      var useNameCard = o['UseNameCard'];
      if (typeof useNameCard === 'string') {
        useNameCard = useNameCard.toUpperCase() === 'TRUE';
      }
      this.useNameCard = useNameCard;
    }

    if (_.has(o, 'OnlyOneSelect')) {
      var onlyOneSelect = o['OnlyOneSelect'];
      if (typeof onlyOneSelect === 'string') {
        onlyOneSelect = onlyOneSelect.toUpperCase() === 'TRUE';
      }
      this.onlyOneSelect = onlyOneSelect;
    }

    if (_.has(o, 'IsSelectCompany')) {
      var isSelectCompany = o['IsSelectCompany'];
      if (typeof isSelectCompany === 'string') {
        isSelectCompany = isSelectCompany.toUpperCase() === 'TRUE';
      }
      this.isSelectCompany = isSelectCompany;
    }
    if (_.has(o, 'SelectCompany')) {
      var isSelectCompany = o['SelectCompany'];
      if (typeof isSelectCompany === 'string') {
        isSelectCompany = isSelectCompany.toUpperCase() === 'TRUE';
      }
      this.isSelectCompany = isSelectCompany;
    }

    if (_.has(o, 'IsSelectChildCompany')) {
      var isSelectChildCompany = o['IsSelectChildCompany'];
      if (typeof isSelectChildCompany === 'string') {
        isSelectChildCompany = isSelectChildCompany.toUpperCase() === 'TRUE';
      }
      this.isSelectChildCompany = isSelectChildCompany;
    }
    if (_.has(o, 'SelectChildCompany')) {
      var isSelectChildCompany = o['SelectChildCompany'];
      if (typeof isSelectChildCompany === 'string') {
        isSelectChildCompany = isSelectChildCompany.toUpperCase() === 'TRUE';
      }
      this.isSelectChildCompany = isSelectChildCompany;
    }

    if (_.has(o, 'showToolbar')) {
      var showToolbar = o['showToolbar'];
      if (typeof showToolbar === 'string') {
        showToolbar = showToolbar.toUpperCase() === 'TRUE';
      }
      this.showToolbar = showToolbar;
    }

    if (_.has(o, 'ReturnType')) {
      var returnType = o['ReturnType'];
      this.returnType = returnType.toUpperCase();
    }

    if (_.has(o, 'OrgMapData')) {
      this.orgMapData = o['OrgMapData'];
    }

    if (_.has(o, 'CallBack')) {
      this.callback = o['CallBack'];
    }

    if (_.has(o, 'onlyUser')) {
      this.onlyUser = o['onlyUser'].toUpperCase() === 'TRUE';
    }
    if (_.has(o, 'OnlyUser')) {
      this.onlyUser = o['OnlyUser'].toUpperCase() === 'TRUE';
    }

    if (_.has(o, 'SelectDeptCode')) {
      this.selectDeptCode = o['SelectDeptCode'];
    }

    if (_.has(o, 'DeptSelection')) {
      var deptSelection = o['DeptSelection'];
      if (typeof deptSelection === 'string') {
        deptSelection = deptSelection.toUpperCase() === 'TRUE';
      }
      this.deptSelection = deptSelection;
    }

    if (_.has(o, 'OneClickToggle')) {
      var oneClickToggle = o['OneClickToggle'];
      if (typeof oneClickToggle === 'string') {
        oneClickToggle = oneClickToggle.toUpperCase() === 'TRUE';
        this.oneClickToggle = oneClickToggle;
      }
    }
  }
}

export default new OrgConfig();



