import AppManager from './app-manager';
import LangHelper from './lang-helper';
import { blockTooltip, releaseTooltip } from './tooltip-extend';

var _ = require('lodash');

var dragTooltip = document.querySelector('#tooltip-drag');
var dragTooltipText = document.querySelector('#tooltip-drag-txt');
var dragTooltipImage = document.querySelector('#tooltip-drag-img');

window.PointerEvent = null;
window.MSPointerEvent = null;
var interact = require('interactjs/interact');

var context = {
  items: [],
  start: '',
};

var status = {
  dragging: false
}

function setDragStartTooltip(e) {
  dragTooltip.style.left = `${e.clientX + 15}px`;
  dragTooltip.style.top = `${e.clientY + 15}px`;
  dragTooltipText.innerText = LangHelper.get(AppManager.language, 'tooltip.drag').replace('$0', context.items.length);
  dragTooltipImage.setAttribute('src', 'public/images/not.svg');
  dragTooltip.style.display = 'inline-block';
}

export default {
  dragging: status.dragging,
  initialize: function() {
    var OrgConfig = require('./org-config').default;

    interact('#orgGrid tbody').styleCursor(false).draggable({
      onstart: function(e) {
        var items = AppManager.controls.grid.getCheckedItems();
        context.items = items;
        context.start = 'grid';

        setDragStartTooltip(e);
        blockTooltip();

        status.dragging = true;
      },
      onmove: function(e) {
        dragTooltip.style.left = `${e.clientX + 15}px`;
        dragTooltip.style.top = `${e.clientY + 15}px`;
      },
      onend: function(e) {
        dragTooltip.style.display = 'none';
        releaseTooltip();

        status.dragging = false;
      },
    });

    if (OrgConfig.deptSelection && Org.config.appType !== 'USER') {
      interact('#orgTree a').styleCursor(false).draggable({
        onstart: function(e) {
          var item = AppManager.controls.tree.getMouseOverItem();
          if (item === null) {
            return false;
          }
          else if (!_.has(item.data, 'EmailAddress')) {
            return false;
          }
          else if (item.data.EmailAddress === null || item.data.EmailAddress === '') {
            return false;
          }
          if (item.data.DisableDND) {
            return false;
          }
          context.items = [item];
          context.start = 'tree';

          setDragStartTooltip(e);
          blockTooltip();

          status.dragging = true;
        },
        onmove: function(e) {
          dragTooltip.style.left = `${e.clientX + 15}px`;
          dragTooltip.style.top = `${e.clientY + 15}px`;
        },
        onend: function(e) {
          dragTooltip.style.display = 'none';
          releaseTooltip();

          status.dragging = false;
        },
      });
    }

    interact('#orgSelectList tbody').styleCursor(false).draggable({
      onstart: function(e) {
        var items = AppManager.controls.selectList.getCheckedItems();
        context.items = items;
        context.start = 'select';

        setDragStartTooltip(e);
        blockTooltip();

        status.dragging = true;
      },
      onmove: function(e) {
        dragTooltip.style.left = `${e.clientX + 15}px`;
        dragTooltip.style.top = `${e.clientY + 15}px`;
      },
      onend: function(e) {
        dragTooltip.style.display = 'none';
        releaseTooltip();

        status.dragging = false;
      },
    });
    interact('#orgToList tbody').styleCursor(false).draggable({
      onstart: function(e) {
        var items = AppManager.controls.to.getCheckedItems();
        context.items = items;
        context.start = 'to';

        setDragStartTooltip(e);
        blockTooltip();

        status.dragging = true;
      },
      onmove: function(e) {
        dragTooltip.style.left = `${e.clientX + 15}px`;
        dragTooltip.style.top = `${e.clientY + 15}px`;
      },
      onend: function(e) {
        dragTooltip.style.display = 'none';
        releaseTooltip();

        status.dragging = false;
      },
    });
    interact('#orgCcList tbody').styleCursor(false).draggable({
      onstart: function(e) {
        var items = AppManager.controls.cc.getCheckedItems();
        context.items = items;
        context.start = 'cc';

        setDragStartTooltip(e);
        blockTooltip();

        status.dragging = true;
      },
      onmove: function(e) {
        dragTooltip.style.left = `${e.clientX + 15}px`;
        dragTooltip.style.top = `${e.clientY + 15}px`;
      },
      onend: function(e) {
        dragTooltip.style.display = 'none';
        releaseTooltip();

        status.dragging = false;
      },
    });
    interact('#orgBccList tbody').styleCursor(false).draggable({
      onstart: function(e) {
        var items = AppManager.controls.bcc.getCheckedItems();
        context.items = items;
        context.start = 'bcc';

        setDragStartTooltip(e);
        blockTooltip();

        status.dragging = true;
      },
      onmove: function(e) {
        dragTooltip.style.left = `${e.clientX + 15}px`;
        dragTooltip.style.top = `${e.clientY + 15}px`;
      },
      onend: function(e) {
        dragTooltip.style.display = 'none';
        releaseTooltip();

        status.dragging = false;
      },
    });

    interact('#orgSelectList').dropzone({
      // checker: function(dEv, e, d, dz, dEl, dg, dgEl) {
      //   if (context.start === 'to') return false;
      //   return d;
      // },
      ondrop: function(e) {
        //var items = AppManager.controls.grid.getCheckedItems();
        var items = context.items;
        for (var i = 0; i < items.length; i++) {
          var unique = items[i].data.UserID;
          if (typeof unique === 'undefined' || unique === '') {
            unique = items[i].data.EmailAddress;
          }
          var result = AppManager.controls.selectList.addItem({
            //unique: items[i].data.EmailAddress,
            unique: unique,
            data: items[i].data,
            columns: [
              items[i].data.DisplayName,
            ]
          });

          // if (result === true) {
          //   if (context.start === 'to') {
          //     AppManager.controls.to.removeItem(items[i].unique);
          //   }
          //   else if (context.start === 'cc') {
          //     AppManager.controls.cc.removeItem(items[i].unique);
          //   }
          //   else if (context.start === 'bcc') {
          //     AppManager.controls.bcc.removeItem(items[i].unique);
          //   }
          // }
        }
        if (AppManager.clearAfterAdd) {
          if (AppManager.controls.grid !== null) {
            AppManager.controls.grid.clearSelection();
          }
        }
        context.items = [];
      },
      ondragenter: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/check.svg');
      },
      ondragleave: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/not.svg');
      },
    });
    interact('#orgToList').dropzone({
      checker: function(dEv, e, d, dz, dEl, dg, dgEl) {
        if (context.start === 'to') return false;
        return d;
      },
      ondrop: function(e) {
        //var items = AppManager.controls.grid.getCheckedItems();
        var items = context.items;
		var isContainContacGroup = false;
        for (var i = 0; i < items.length; i++) {
          var unique = items[i].data.UserID;
          if (typeof unique === 'undefined' || unique === '') {
            unique = items[i].data.EmailAddress;
          }
		  
		  if (items[i].data.EntryType == 3) {
				isContainContacGroup = true;
          }
		  
          var result = AppManager.controls.to.addItem({
            //unique: items[i].data.EmailAddress,
            unique: unique,
            data: items[i].data,
            columns: [
              items[i].data.DisplayName,
            ]
          });

          if (result === true) {
            if (context.start === 'to') {
              AppManager.controls.to.removeItem(items[i].unique);
            }
            else if (context.start === 'cc') {
              AppManager.controls.cc.removeItem(items[i].unique);
            }
            else if (context.start === 'bcc') {
              AppManager.controls.bcc.removeItem(items[i].unique);
            }
          }
        }
		
        if (AppManager.clearAfterAdd) {
          if (AppManager.controls.grid !== null) {
            AppManager.controls.grid.clearSelection();
			// if (Org.config.warnContactGroup === true && isContainContacGroup == true) {
				// alert('     .')
			// }
          }
        }
        context.items = [];
      },
      ondragenter: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/check.svg');
      },
      ondragleave: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/not.svg');
      },
    });
    interact('#orgCcList').dropzone({
      checker: function(dEv, e, d, dz, dEl, dg, dgEl) {
        if (context.start === 'cc') return false;
        return d;
      },
      ondrop: function(e) {
        //var items = AppManager.controls.grid.getCheckedItems();
        var items = context.items;
		
		var isContainContacGroup = false;
        for (var i = 0; i < items.length; i++) {
          var unique = items[i].data.UserID;
          if (typeof unique === 'undefined' || unique === '') {
            unique = items[i].data.EmailAddress;
          }
		  if (items[i].data.EntryType == 3) {
				isContainContacGroup = true;
          }
		  var result = AppManager.controls.cc.addItem({
            //unique: items[i].data.EmailAddress,
            unique: unique,
            data: items[i].data,
            columns: [
              items[i].data.DisplayName,
            ]
          });

          if (result === true) {
            if (context.start === 'to') {
              AppManager.controls.to.removeItem(items[i].unique);
            }
            else if (context.start === 'cc') {
              AppManager.controls.cc.removeItem(items[i].unique);
            }
            else if (context.start === 'bcc') {
              AppManager.controls.bcc.removeItem(items[i].unique);
            }
          }
        }
		
	    if (AppManager.clearAfterAdd) {
          if (AppManager.controls.grid !== null) {
            AppManager.controls.grid.clearSelection();
			// if (Org.config.warnContactGroup === true &&  isContainContacGroup == true) {
				// alert('     .')
		    // }
          }
        }
        context.items = [];
      },
      ondragenter: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/check.svg');
      },
      ondragleave: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/not.svg');
      },
    });
    interact('#orgBccList').dropzone({
      checker: function(dEv, e, d, dz, dEl, dg, dgEl) {
        if (context.start === 'bcc') return false;
        return d;
      },
      ondrop: function(e) {
        //var items = AppManager.controls.grid.getCheckedItems();
        var items = context.items;
		var isContainContacGroup = false;
        for (var i = 0; i < items.length; i++) {
          var unique = items[i].data.UserID;
          if (typeof unique === 'undefined' || unique === '') {
            unique = items[i].data.EmailAddress;
          }
		  if (items[i].data.EntryType == 3) {
				isContainContacGroup = true;
          }
          var result = AppManager.controls.bcc.addItem({
            //unique: items[i].data.EmailAddress,
            unique: unique,
            data: items[i].data,
            columns: [
              items[i].data.DisplayName,
            ]
          });

          if (result === true) {
            if (context.start === 'to') {
              AppManager.controls.to.removeItem(items[i].unique);
            }
            else if (context.start === 'cc') {
              AppManager.controls.cc.removeItem(items[i].unique);
            }
            else if (context.start === 'bcc') {
              AppManager.controls.bcc.removeItem(items[i].unique);
            }
          }
        }
		
        if (AppManager.clearAfterAdd) {
          if (AppManager.controls.grid !== null) {
            AppManager.controls.grid.clearSelection();
			// if (Org.config.warnContactGroup === true && isContainContacGroup == true) {
				// alert('     .')
		    // }
          }
        }
        context.items = [];
      },
      ondragenter: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/check.svg');
      },
      ondragleave: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/not.svg');
      },
    });

    interact('#orgGrid tbody').dropzone({
      checker: function(dEv, e, d, dz, dEl, dg, dgEl) {
        if (context.start === 'to' || context.start === 'cc' || context.start === 'bcc' || context.start === 'select') {
          return d;
        }
        return false;
      },
      ondrop: function(e) {
        //var items = AppManager.controls.grid.getCheckedItems();
        var items = context.items;
        for (var i = 0; i < items.length; i++) {
          if (context.start === 'to') {
            AppManager.controls.to.removeItem(items[i].unique);
          }
          else if (context.start === 'cc') {
            AppManager.controls.cc.removeItem(items[i].unique);
          }
          else if (context.start === 'bcc') {
            AppManager.controls.bcc.removeItem(items[i].unique);
          }
          else if (context.start === 'select') {
            AppManager.controls.selectList.removeItem(items[i].unique);
          }
        }
        context.items = [];
      },
      ondragenter: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/check.svg');
      },
      ondragleave: function(e) {
        dragTooltipImage.setAttribute('src', 'public/images/not.svg');
      },
    });

    if (Org.config.appType === 'DEPT') {
      if (Org.config.onlyOneSelect === false) {
        interact('#pnl-dept-tree').dropzone({
          checker: function(dEv, e, d, dz, dEl, dg, dgEl) {
            if (context.start === 'select') {
              return d;
            }
            return false;
          },
          ondrop: function(e) {
            //var items = AppManager.controls.grid.getCheckedItems();
            var items = context.items;
            for (var i = 0; i < items.length; i++) {
              if (context.start === 'select') {
                AppManager.controls.selectList.removeItem(items[i].unique);
              }
            }
            context.items = [];
          },
          ondragenter: function(e) {
            dragTooltipImage.setAttribute('src', 'public/images/check.svg');
          },
          ondragleave: function(e) {
            dragTooltipImage.setAttribute('src', 'public/images/not.svg');
          },
        });
      }
    }
  }
}



