import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');
import 'whatwg-fetch';
import Guid from 'guid';
import GridRow from './grid-row.jsx';
import GridHeader from './grid-header.jsx';
import GridItem from './grid-item';
import GridItemCell from './grid-item-cell';
import TooltipManager from 'tooltip';

var $ = require('jquery');

if (typeof Promise === typeof undefined) {
  var Promise = require('es6-promise/es6-promise');
}

class Grid extends React.Component {
  constructor(props) {
    super(props);
    this.onColumnWidthChange = this.onColumnWidthChange.bind(this);
    this.getCheckedItems = this.getCheckedItems.bind(this);
    this.onViewScroll = this.onViewScroll.bind(this);
    this.syncScroll = this.syncScroll.bind(this);
    this.onSingleSelection = this.onSingleSelection.bind(this);
    this.onToggleSelection = this.onToggleSelection.bind(this);
    this.onRangeSelection = this.onRangeSelection.bind(this);
    this.onSelectAll = this.onSelectAll.bind(this);
    this.state = {
      header: null,
      onColumnWidthChange: this.onColumnWidthChange,
      items: [],
      selectAll: false,
      data: null,
    };

    this.selectCache = null;
    this.enableToggle = true;
    this.enableOneClickToggle = false;

    window.addEventListener('resize', function(e) {
      this.syncScroll();
    }.bind(this));
  }

  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }

  clear() {
    this.selectCache = null;
    this.state.items = [];
    this.state.selectAll = this.isSelectAll();
    this.setState(this.state);
  }

  clearSelection() {
    this.selectCache = null;
    for (var i = 0; i < this.state.items.length; i++) {
      this.state.items[i].checked = false;
    }
    this.state.selectAll = this.isSelectAll();
    this.setState(this.state);
  }

  fetchData(url, option, before, fn, fe) {
    if (before !== null && typeof before === 'function') {
      before();
    }


    var ajaxOption = option;
    ajaxOption.url = url;
    $.ajax(ajaxOption)
    .done(function(j) {
      this.clear();
      if (typeof fn !== typeof undefined) {
        var items = fn(j);
        this.addItems(items);
      }
    }.bind(this))
    .fail(function(err) {
      this.clear();
      if (typeof fe !== typeof undefined) {
        fe(err);
      }
    }.bind(this));

    //fetch(url, option)
    //.then(function(response) {
    //  if (response.ok === true) {
    //    return response.json();
    //  }
    //  else {
    //    this.clear();
    //    if (typeof fe !== typeof undefined) {
    //      fe();
    //    }
    //  }
    //}.bind(this))
    //.then(function(j) {
    //  this.clear();
    //  if (typeof fn !== typeof undefined) {
    //    var items = fn(j);
    //    this.addItems(items);
    //  }
    //}.bind(this))
    //.catch(function(err) {
    //  this.clear();
    //  if (typeof fe !== typeof undefined) {
    //    fe(err);
    //  }
    //}.bind(this));
  }

  getData() {
    return this.state.data;
  }

  getItemByUnique(unique) {
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].unique === unique) {
        return this.state.items[i];
      }
    }
    return null;
  }

  setData(data) {
    this.state.data = data;
    this.setState(this.state);
  }

  /**
   * set header
   * @param [{
   * unique: Object,
   * text: string,
   * columnName: string,
   * minWidth: integer,
   * maxWidth: integer,
   * width: integer,
   * }] header header object
   */
  setHeader(header) {
    var item = new GridItem();

    //for (var col of header) {
    for (var i = 0; i < header.length; i++) {
      var col = header[i];
      var cell = new GridItemCell();

      if (typeof col === 'string') {
        cell.text = col;
        cell.columnName = col;
        cell.unique = col;
      }
      else {
        cell.text = col.text;
        cell.columnName = _.get(col, 'columnName', col.text);
        cell.unique = _.get(col, 'unique', Guid.v4());
        cell.width = _.get(col, 'width', cell.width);
        cell.minWidth = _.get(col, 'minWidth', cell.width);
        cell.maxWidth = _.get(col, 'maxWidth', cell.width);
      }

      item.addCell(cell);
    }

    this.state.header = item;

    this.setState(this.state);
  }

  /**
   * add item
   * @param [{
   * unique: Object,
   * text: string,
   * selected: boolean,
   * data: Object
   * }] item item object
   */
  addItem(item, index) {
    if (this.state.header === null) {
      //TODO: log
      return false;
    }
    var columns = item;
    if (!Array.isArray(item)) {
      //TODO: log
      columns = _.get(item, 'columns', null);
      if (columns === null) {
        return false;
      }
    }
    var itemLength = columns.length;
    if (this.state.header.cells.length !== itemLength) {
      //TODO: log
      return false;
    }

    var unique = _.get(item, 'unique', Guid.v4());
    for (var i = 0; i < this.state.items.length; i++) {
      var current = this.state.items[i];
      if (current.unique === unique) {
        return false;
      }
    }

    var itemObject = new GridItem();
    itemObject.unique = unique;
    itemObject.data = _.get(item, 'data', null);

    for (var i = 0; i < itemLength; i++) {
      var cellObject = new GridItemCell();

      if (typeof columns[i] === 'string') {
        cellObject.text = columns[i];
        cellObject.unique = Guid.v4();
        cellObject.columnName = this.state.header.cells[i].columnName;
        cellObject.width = this.state.header.cells[i].width;
      }
      else {
        cellObject.text = columns[i].text;
        cellObject.html = _.get(columns[i], 'html', null);
        cellObject.unique = _.get(columns[i], 'unique', Guid.v4());
        cellObject.columnName = this.state.header.cells[i].columnName;
        cellObject.width = this.state.header.cells[i].width;
        cellObject.data = _.get(columns[i], 'data', null);
        cellObject.selected = _.get(columns[i], 'selected', false);
        cellObject.checked = _.get(columns[i], 'checked', false);
        cellObject.useIcon = _.get(columns[i], 'useIcon', cellObject.useIcon);
        cellObject.iconClass = _.get(columns[i], 'iconClass', cellObject.iconClass);
      }

      itemObject.addCell(cellObject);
    }
    if(index != undefined && index != null && typeof index === 'number'){
        this.state.items.splice(index, 0, itemObject);
    }
    else{
        this.state.items.push(itemObject);
    }
    
    this.state.selectAll = this.isSelectAll();
    this.setState(this.state);

    return true;
  }

  convertItem(item) {
    if (this.state.header === null) {
      //TODO: log
      return false;
    }
    var columns = item;
    if (!Array.isArray(item)) {
      //TODO: log
      columns = _.get(item, 'columns', null);
      if (columns === null) {
        return false;
      }
    }
    var itemLength = columns.length;
    if (this.state.header.cells.length !== itemLength) {
      //TODO: log
      return false;
    }

    var unique = _.get(item, 'unique', Guid.v4());
    for (var i = 0; i < this.state.items.length; i++) {
      var current = this.state.items[i];
      if (current.unique === unique) {
        return false;
      }
    }

    var itemObject = new GridItem();
    itemObject.unique = unique;
    itemObject.data = _.get(item, 'data', null);

    for (var i = 0; i < itemLength; i++) {
      var cellObject = new GridItemCell();

      if (typeof columns[i] === 'string') {
        cellObject.text = columns[i];
        cellObject.unique = Guid.v4();
        cellObject.columnName = this.state.header.cells[i].columnName;
        cellObject.width = this.state.header.cells[i].width;
      }
      else {
        cellObject.text = columns[i].text;
        cellObject.html = _.get(columns[i], 'html', null);
        cellObject.unique = _.get(columns[i], 'unique', Guid.v4());
        cellObject.columnName = this.state.header.cells[i].columnName;
        cellObject.width = this.state.header.cells[i].width;
        cellObject.data = _.get(columns[i], 'data', null);
        cellObject.selected = _.get(columns[i], 'selected', false);
        cellObject.checked = _.get(columns[i], 'checked', false);
        cellObject.useIcon = _.get(columns[i], 'useIcon', cellObject.useIcon);
        cellObject.iconClass = _.get(columns[i], 'iconClass', cellObject.iconClass);
      }

      itemObject.addCell(cellObject);
    }

    return itemObject;
  }

  addItems(items) {
    if (Array.isArray(items)) {
      var objs = [];
      for (var i = 0; i < items.length; i++) {
        var item = items[i];
        var o = this.convertItem(item);
        objs.push(o);
        //this.addItem(item);
      }

      this.state.items = objs;
      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
    else {
      this.addItem(items);
    }
  }

  removeItem(unique) {
    var removeIndex = -1;
    if (Array.isArray(unique)) {
      //for (var u of unique) {
      for (var i = 0; i < unique.length; i++) {
        var u = unique[i];
        this.removeItem(u);
      }
    }
    else {
      var itemLength = this.state.items.length;
      for (var i = 0; i < itemLength; i++) {
        if (this.state.items[i].unique === unique) {
          this.state.items.splice(i, 1);
          removeIndex = i;
          break;
        }
      }

      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
    return removeIndex;
  }

  getCheckedItems() {
    var items = [];
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].checked === true) {
        items.push(this.state.items[i]);
      }
    }
    return items;
  }

  getItems() {
    return this.state.items;
  }

  onColumnWidthChange(e) {
    // e = grid-column
    this.setState(this.state);
  }

  onViewScroll(e) {
    if (this.state.header !== null && this.state.header.cells.length > 1) {
      this.refs.headerTable.style.width = this.refs.contentDiv.clientWidth + 'px';
    }
    this.refs.headerWrapper.style.left = -e.target.scrollLeft + 'px';
  }

  syncScroll() {
    if (this.refs.headerWrapper !== null && this.refs.viewScroll !== null) {
      if (this.state.header !== null && this.state.header.cells.length > 1) {
        this.refs.headerTable.style.width = this.refs.contentDiv.clientWidth + 'px';
      }
      this.refs.headerWrapper.style.left = -this.refs.viewScroll.scrollLeft + 'px';
    }
  }

  getTableWidth() {
    var width = 40;
    if (this.state.header !== null) {
      for (var i = 0; i < this.state.header.cells; i++) {
        width += this.state.header.cells[i].width;
      }
    }
    return width;
  }

  isSelectAll() {
    var length = this.state.items.length;
    if (length === 0) return false;
    for (var i = 0; i < length; i++) {
      if (this.state.items[i].checked === false) return false;
    }
    return true;
  }

  onSelectAll(selected) {
    if (this.enableToggle === false) {
      return;
    }
    if (this.state.items.length === 0) {
      this.state.selectAll = false;
    }
    else {
      this.state.selectAll = selected;
      for (var i = 0; i < this.state.items.length; i++) {
        this.state.items[i].checked = selected;
      }
    }
    this.setState(this.state);
  }

  onSingleSelection(e) {
    //this.selectCache = e.props.item;
    
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i] != e.props.item) {
        this.state.items[i].checked = false;
      }
    }
    e.props.item.checked = !e.props.item.checked;
    if (e.props.item.checked === true) {
      this.selectCache = e.props.item;
    }
    else {
      this.selectCache = null;
    }
    this.state.selectAll = this.isSelectAll();
    this.setState(this.state);
  }

  onToggleSelection(e) {
    if (this.enableToggle) {
      e.props.item.checked = !e.props.item.checked;

      if (e.props.item.checked) {
        this.selectCache = e.props.item;
      }

      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
    else {
      this.onSingleSelection(e);
    }
  }

  onRangeSelection(e) {
    if (this.enableToggle === false) {
      this.onSingleSelection(e);
    }

    if (this.selectCache === null) {
      for (var i = 0; i < this.state.items.length; i++) {
        if (this.state.items[i] != e.props.item) {
          this.state.items[i].checked = true;
        }
        else {
          this.state.items[i].checked = true;
          this.selectCache = this.state.items[i];
          break;
        }
      }

      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
    else {
      // basic validation
      if (e.props.item === this.selectCache) return;

      var inRange = false;
      for (var i = 0; i < this.state.items.length; i++) {
        if (inRange === false) {
          if (this.state.items[i] === this.selectCache || this.state.items[i] === e.props.item) {
            inRange = true;
          }
        }
        else {
          if (this.state.items[i] === this.selectCache || this.state.items[i] === e.props.item) {
            inRange = false;
            this.state.items[i].checked = true;
            continue;
          }
        }

        if (inRange === true) {
          this.state.items[i].checked = true;
        }
        else {
          this.state.items[i].checked = false;
        }
      }

      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
  }

  renderRow() {
    var rows = [];
    //for (var item of this.state.items) {
    for (var i = 0; i < this.state.items.length; i++) {
      var item = this.state.items[i];
      rows.push(
        <GridRow key={item.unique}
                 item={item}
                 enableOneClickToggle={this.enableOneClickToggle}
                 getCheckedItems={this.getCheckedItems}
                 onSingleSelection={this.onSingleSelection}
                 onRangeSelection={this.onRangeSelection}
                 onToggleSelection={this.onToggleSelection}
                 />
      );
    }
    return rows;
  }

  renderColGroup() {
    var colGroup = [];
    if (this.state.header !== null) {
      //for (var cell of this.state.header.cells) {
      for (var i = 0; i < this.state.header.cells.length; i++) {
        var cell = this.state.header.cells[i];
        if (cell.width > 0) {
          colGroup.push(
            <col width={cell.width} />
          );
        }
        else {
          colGroup.push(
            <col />
          );
        }
      }
    }
    return colGroup;
  }

  componentDidUpdate() {
    this.syncScroll();
  }

  render() {
    var tableStyle = {
      width: this.getTableWidth() + 'px',
    };
    var rows = this.renderRow();
    var headerTableStyle = {};
    if (this.state.header !== null && this.state.header.cells.length > 1) {
      if (rows.length > 0) {
        headerTableStyle.width = rows[0].clientWidth + 'px';
      }
    }

    return (
      <div className="grid_container" ref="container">
        <div className="tbl_vert" ref="headerWrapper" style={{position: 'relative'}}>
          <table ref="headerTable" style={headerTableStyle}>
            <colgroup>
              <col width="30" />
              {this.renderColGroup()}
            </colgroup>
            <thead>
              <GridHeader header={this.state.header}
                          showCheckAll={this.enableToggle}
                          selectAll={this.state.selectAll}
                          onResizeMouseDown={this.onResizeMouseDown}
                          onSelectAll={this.onSelectAll}
                          />
            </thead>
          </table>
        </div>
        <div className="set_scroll"
             ref="viewScroll"
             style={{width: '100%', height: 'calc(100% - 30px)'}}
             onScroll={this.onViewScroll}>
          <div className="tbl_vert" ref="contentDiv">
            <table>
              <colgroup>
                <col width="30" />
                {this.renderColGroup()}
              </colgroup>
              <tbody>
                {rows}
              </tbody>
            </table>
          </div>
        </div>
      </div>
    );
  }
}

module.exports = Grid;

export default Grid;



