import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');

class GridColumn extends React.Component {
  constructor(props) {
    super(props);
  }

  render() {
    return (
      <td className="tbl_data">{this.props.cell.text}</td>
    );
  }
}



