var $ = require('jquery');

var SplitPanelRefresh = false;

(function($, undefined) {
  function getHandle(selector, $el) {
    if (selector.trim()[0] === ">") {
      selector = selector.trim().replace(/^>\s*/, "");

      return $el.find(selector);
    }
    return selector ? $(selector) : $el;
  }

  if ($.fn.resizable)
  return;
  
var SplitPanelRefresh = false;

    // Resizable Event Process **************************************
    $.fn.resizable = function fnResizable(options) {
        var PanelSplitMode = {
            LeftGridRight : 0,
            LeftGrid : 1,
            LeftRight : 2            
        }
        // window resize   Fixed   Panel
        var FixedPanel =
        {
            LeftPanel : 0,
            RightPanel : 1
        }
        
        var opt = {
            // selector for handle that starts dragging
            handleSelector: null,
            // selector for shadow handle that starts dragging
            handleSelectorShadow: null,
            // resize the width
            resizeWidth: true,
            // resize the height
            resizeHeight: true,
            // the side that the width resizing is relative to
            resizeWidthFrom: 'right',
            // the side that the height resizing is relative to
            resizeHeightFrom: 'bottom',
            // hook into start drag operation (event passed)
            onDragStart: null,
            // hook into stop drag operation (event passed)
            onDragEnd: null,
            // hook into each drag operation (event passed)
            onDrag: null,
            // disable touch-action on $handle
            // prevents browser level actions like forward back gestures
            touchActionNone: true
        };

        if (typeof options == "object") opt = $.extend(opt, options);

        return this.each(function () {
            
            // panelSplitMode =
            // 0 : left panel, grid panel, list(right panel);
            // 1 : left panel, grid panel 
            // 2 : left panel, list(right) panel
            var panelSplitMode; 
            var fixedPanel = FixedPanel.LeftPanel;
             
            var startPos, startTransition;
            var gridOrgWidth;
            var leftOrgWidth;
            var rightOrgWidth;

            var $el = $(this);

            var $handle = getHandle(opt.handleSelector, $el);
            var $handleShadow = getHandle(opt.handleSelectorShadow, $el);

            var leftPanelMinWidth = parseInt($(".orgchart-panel-left").css("min-width"), 10);
            var gridPanelMinWidth = parseInt($(".orgchart-panel-grid").css("min-width"), 10);
            var rightPanelMinWidth = parseInt($(".orgchart-panel-right").css("min-width"), 10);
            
            // 2016-05-30 daddygom,   
            // hidden / visible   .
            
            var gridPanelDisplay = $(".orgchart-panel-grid").css("display");
            var rightPanelDisplay = $(".orgchart-panel-right").css("display");
            
            if(gridPanelDisplay == "none" && rightPanelDisplay != "none")
            {
                panelSplitMode = PanelSplitMode.LeftRight;
            }
            else if(gridPanelDisplay != "none" && rightPanelDisplay == "none")
            {
                panelSplitMode = PanelSplitMode.LeftGrid;
            }
            else
            {
                panelSplitMode = PanelSplitMode.LeftGridRight;
            }

            var newLeftWidth;
            var newGridWidth;
            var newRightWidth;

            if (opt.touchActionNone) {
                $handle.css("touch-action", "none");
                $handleShadow.css("touch-action", "none");
            }

            $el.addClass("resizable");
            $handle.bind('mousedown.rsz touchstart.rsz', startDragging);
            $handleShadow.bind('mousedown.rsz touchstart.rsz', startDragging);

            setPageContainerMinWidth();
            setGridPanelWidth();

            // noop ******************************************************
            function noop(e) {
                e.stopPropagation();
                e.preventDefault();
            };

            //    ********************************************
            function startDragging(e) {
                startPos = getMousePos(e);
                startPos.width = parseInt($el.width(), 10);
                startPos.height = parseInt($el.height(), 10);
                
                if(SplitPanelRefresh == false)
                {
                    // none  div display option block 
                    $handleShadow.css("display", "block");
                }
                
                
                switch(panelSplitMode)
                {   
                    case PanelSplitMode.LeftGrid:
                        gridOrgWidth = $(".orgchart-panel-grid").width();
                        leftOrgWidth = $(".orgchart-panel-container").width()
                        - gridOrgWidth
                        - $(".orgchart-splitter-left").width()
                        - 2; // splitter border 2px * 2(left, right);
                        break;
                        
                    case PanelSplitMode.LeftRight:
                        rightOrgWidth = $(".orgchart-panel-right").width();
                        leftOrgWidth = $(".orgchart-panel-container").width()
                        - rightOrgWidth
                        - $(".orgchart-splitter-left").width()
                        - 2; // splitter border 2px * 2(left, right);
                        break;
                        
                    case PanelSplitMode.LeftGridRight:
                    default :
                        leftOrgWidth = $(".orgchart-panel-left").width();
                        gridOrgWidth = $(".orgchart-panel-grid").width();
                        rightOrgWidth = $(".orgchart-panel-container").width()
                        - gridOrgWidth
                        - leftOrgWidth
                        - $(".orgchart-splitter-left").width()
                        - $(".orgchart-splitter-right").width()
                        - 4; // splitter border 2px * 2(left, right);
                        break;
                }


                startTransition = $el.css("transition");
                $el.css("transition", "none");

                if (opt.onDragStart) {
                    if (opt.onDragStart(e, $el, opt) === false)
                        return;
                }
                opt.dragFunc = doDrag;

                $(document).bind('mousemove.rsz', opt.dragFunc);
                $(document).bind('mouseup.rsz', stopDragging);

                if (window.Touch || navigator.maxTouchPoints) {
                    $(document).bind('touchmove.rsz', opt.dragFunc);
                    $(document).bind('touchend.rsz', stopDragging);
                }

                $(document).bind('selectstart.rsz', noop); // disable selection

                // 2016-05-03 daddygom, Splitter Height 
                var gridHeight = $(".orgchart-panel-left").height();
                $handleShadow.css("height", gridHeight);
            }

            //    ********************************************
            function doDrag(e) {
                if (SplitPanelRefresh == true) {
                    refreshPanel(e);
                    refreshPanelForSplitter(e)
                }
                else {
                    refreshSplitter(e);
                }
            }

            // - Splitter   ***********************************
            // SplitPanelRefresh == false
            function refreshSplitter(e) {
                var pos = getMousePos(e), newWidth, newHeight;
                var moveGap = pos.x - startPos.x;



                switch (opt.handleSelector) {
                    // LEFT SPLITTER MOVE EVENT *********************
                    case ".orgchart-splitter-left":
                        
                        switch (panelSplitMode)
                        {
                            case PanelSplitMode.LeftRight:
                            
                                newLeftWidth = leftOrgWidth + moveGap;
                                newRightWidth = rightOrgWidth - moveGap;
                                // 1.  left width min-width   min-width 
                                if (newLeftWidth < leftPanelMinWidth) {
                                    var gap = newLeftWidth - leftPanelMinWidth;
                                    newLeftWidth += gap;
                                    newRightWidth += gap;
                                    return;
                                }
                                //  grid Width gridPanelMinWidth   return;
                                if (newRightWidth < rightPanelMinWidth) {
                                    var gap = newRightWidth - rightPanelMinWidth;
                                    newLeftWidth += gap;
                                    newRightWidth += gap;
                                    return;
                                }

                                // 2. LEFT SPLITTER   GRID PANEL MIN-WIDTH  
                                //      RIGHT PANEL   
                                if (newRightWidth < rightPanelMinWidth) {
                                    return;
                                }            
                                break;
                                                           
                            case PanelSplitMode.LeftGrid:
                            case PanelSplitMode.LeftGridRight:
                            default:
                            
                                newLeftWidth = leftOrgWidth + moveGap;
                                newGridWidth = gridOrgWidth - moveGap;
                                // 1.  left width min-width   min-width 
                                if (newLeftWidth < leftPanelMinWidth) {
                                    var gap = newLeftWidth - leftPanelMinWidth;
                                    newGridWidth += gap;
                                    newLeftWidth += gap;
                                    return;
                                }
                                //  grid Width gridPanelMinWidth   return;
                                if (newGridWidth < gridPanelMinWidth) {
                                    var gap = newGridWidth - gridPanelMinWidth;
                                    newGridWidth += gap;
                                    newLeftWidth += gap;
                                    return;
                                }

                                // 2. LEFT SPLITTER   GRID PANEL MIN-WIDTH  
                                //      RIGHT PANEL   
                                if (newGridWidth < gridPanelMinWidth) {
                                    return;
                                }            
                                break;
                        }                        
            
                        break;

                        // RIGHT SPLITTER MOVE EVENT ********************
                    case ".orgchart-splitter-right":
                        // 2.  grid grid-width min-width   min-width 
                        newGridWidth = gridOrgWidth + moveGap;
                        newRightWidth = rightOrgWidth - moveGap;

                        // new Grid Width grid panel min width  
                        if (newGridWidth < gridPanelMinWidth) {
                            var gap = newGridWidth - gridPanelMinWidth;
                            newGridWidth += gap;
                            newRightWidth += gap;
                            return;
                        }

                        // new right width right panel min width  
                        if (newRightWidth < rightPanelMinWidth) {
                            var gap = newRightWidth - rightPanelMinWidth;
                            newGridWidth += gap;
                            newRightWidth += gap;
                            return;
                        }
                        break;
                }
                //var shadowScrollLeft = $("#containerBody").scrollLeft();
                //2016-05-02 daddygom, IE / FIREFOX scrollLeft    .
                var shadowScrollLeft = window.pageXOffset || document.documentElement.scrollLeft;
                $handleShadow.css("left", pos.x + shadowScrollLeft);
            }

            // SplitPanelRefresh == false
            //      
            function refreshPanelForSplitter(e) {
                $(".orgchart-panel-left").width(newLeftWidth);
                $(".orgchart-panel-grid").width(newGridWidth);
                $(".orgchart-panel-right").width(newRightWidth);
            }

            // -  split panel   ******************************
            // SplitPanelRefresh == true
            function refreshPanel(e) {
                var pos = getMousePos(e);
                var moveGap = pos.x - startPos.x;

                // Left / Right Spliiter move event 
                switch (opt.handleSelector) {
                    // LEFT SPLITTER MOVE EVENT *********************
                    case ".orgchart-splitter-left":
                        newLeftWidth = leftOrgWidth + moveGap;
                        newGridWidth = gridOrgWidth - moveGap;
                        // 1.  left width min-width   min-width 
                        if (newLeftWidth < leftPanelMinWidth) {
                            var gap = newLeftWidth - leftPanelMinWidth;
                            newGridWidth += gap;
                            newLeftWidth += gap;
                            return;
                        }
                        //  grid Width gridPanelMinWidth   return;
                        if (newGridWidth < gridPanelMinWidth) {
                            var gap = newGridWidth - gridPanelMinWidth;
                            newGridWidth += gap;
                            newLeftWidth += gap;
                            return;
                        }

                        // 2. LEFT SPLITTER   GRID PANEL MIN-WIDTH  
                        //      RIGHT PANEL   
                        if (newGridWidth < gridPanelMinWidth) {
                            return;
                        }

                        break;

                        // RIGHT SPLITTER MOVE EVENT ********************
                    case ".orgchart-splitter-right":
                        // 2.  grid grid-width min-width   min-width 

                        newGridWidth = gridOrgWidth + moveGap;
                        newRightWidth = rightOrgWidth - moveGap;

                        if (newGridWidth < gridPanelMinWidth) {
                            var gap = newGridWidth - gridPanelMinWidth;
                            newGridWidth += gap;
                            newRightWidth += gap;

                            $(".orgchart-panel-grid").width(newGridWidth);
                            $(".orgchart-panel-right").width(newRightWidth);
                            return;
                        }
                        if (newRightWidth < rightPanelMinWidth) {
                            var gap = newRightWidth - rightPanelMinWidth;
                            newGridWidth += gap;
                            newRightWidth += gap;

                            $(".orgchart-panel-grid").width(newGridWidth);
                            $(".orgchart-panel-right").width(newRightWidth);
                            return;
                        }
                        break;
                }
            }

            // 2016-04-25 daddygom,
            // grid panel    
            // 100% - x px;  
            function setGridPanelWidth() {
                
                switch(panelSplitMode)
                {
                    case PanelSplitMode.LeftGrid:
                        
                        if(fixedPanel == FixedPanel.LeftPanel)
                        {
                                var leftWidth = $(".orgchart-panel-left").width();
                                var leftSplitter = $(".orgchart-splitter-left").width();
                                var remainWidth = leftWidth + leftSplitter + 2;
                                var rightPercentResult = "calc(100% - " + remainWidth + "px)";
                                $(".orgchart-panel-left").css("width", leftWidth);
                                $(".orgchart-panel-grid").css("width", rightPercentResult);
                        }
                        else if(fixedPanel == FixedPanel.RightPanel)
                        {
                                var gridWidth = $(".orgchart-panel-grid").width();
                                var leftSplitter = $(".orgchart-splitter-left").width();
                                var remainWidth = gridWidth + leftSplitter + 2;
                                var leftPercentResult = "calc(100% - " + remainWidth + "px)";
                                $(".orgchart-panel-left").css("width", leftPercentResult);
                        } 
                        break;
                        
                    case PanelSplitMode.LeftRight:

                        if(fixedPanel == FixedPanel.LeftPanel)
                        {
                                var leftWidth = $(".orgchart-panel-left").width();
                                var leftSplitter = $(".orgchart-splitter-left").width();
                                var remainWidth = leftWidth + leftSplitter + 2;
                                var rightPercentResult = "calc(100% - " + remainWidth + "px)";
                                $(".orgchart-panel-left").css("width", leftWidth);
                                $(".orgchart-panel-right").css("width", rightPercentResult);
                        }
                        else if (fixedPanel == FixedPanel.RightPanel)
                        {
                                var rightWidth = $(".orgchart-panel-right").width();
                                var leftSplitter = $(".orgchart-splitter-left").width();
                                var remainWidth = rightWidth + leftSplitter + 2;
                                var leftPercentResult = "calc(100% - " + remainWidth + "px)";
                                $(".orgchart-panel-left").css("width", leftPercentResult); 
                        }                                
                        break;
                        
                    case PanelSplitMode.LeftGridRight:
                    default: 
                        var gridWidth = $(".orgchart-panel-grid").width();
                        var pageContainerWidth = $(".orgchart-panel-container").width();
                        var gridContainerGap = pageContainerWidth - gridWidth;
                        var gridPercentResult = "calc(100% - " + gridContainerGap + "px)";
                        $(".orgchart-panel-grid").css("width", gridPercentResult);
                        break;
                }
            }

            // 2016-04-26 daddygom,
            // orgchart scrollbar     calculator
            function setPageContainerMinWidth() {

                switch(panelSplitMode)
                {
                    case PanelSplitMode.LeftGrid:
                        
                        if(fixedPanel == FixedPanel.LeftPanel)
                        {
                                var splitterWidthTotal = $(".orgchart-splitter-left").width() + 2;
                                var leftWidth = $(".orgchart-panel-left").width();
                                var pageContainerMinWidth = gridPanelMinWidth + splitterWidthTotal + leftWidth;
                                var containerWidth = $(".orgchart-panel-container").width();
                                $(".orgchart-page-container").css("min-width", pageContainerMinWidth + "px");

                                if (window.innerWidth < containerWidth) {
                                    $(".orgchart-page-container").css("min-width", containerWidth + "px");
                                }
                        }
                        else if(fixedPanel == FixedPanel.RightPanel)
                        {
                                var splitterWidthTotal = $(".orgchart-splitter-left").width() + 2;
                                var gridWidth = $(".orgchart-panel-grid").width();
                                var pageContainerMinWidth = leftPanelMinWidth + splitterWidthTotal + gridWidth;
                                var containerWidth = $(".orgchart-panel-container").width();
                                // scroll bar    min-width    .
                                // because ->        .
                                //     grid  resize  min-width  
                                $(".orgchart-page-container").css("min-width", pageContainerMinWidth + "px");

                                if (window.innerWidth < containerWidth) {
                                    $(".orgchart-page-container").css("min-width", containerWidth + "px");
                                }
                        }      
                        break;
                        
                    case PanelSplitMode.LeftRight:
                    
                        if(fixedPanel == FixedPanel.LeftPanel)
                        {
                                var splitterWidthTotal = $(".orgchart-splitter-left").width() + 2;
                                var leftWidth = $(".orgchart-panel-left").width();
                                var pageContainerMinWidth = rightPanelMinWidth + splitterWidthTotal + leftWidth;
                                var containerWidth = $(".orgchart-panel-container").width();
                                $(".orgchart-page-container").css("min-width", pageContainerMinWidth + "px");

                                if (window.innerWidth < containerWidth) {
                                    $(".orgchart-page-container").css("min-width", containerWidth + "px");
                                }
                        }   
                        else if(fixedPanel == FixedPanel.RightPanel)
                        {
                                var splitterWidthTotal = $(".orgchart-splitter-left").width() + 2;
                                var rightWidth = $(".orgchart-panel-right").width();
                                var pageContainerMinWidth = leftPanelMinWidth + splitterWidthTotal + rightWidth;
                                var containerWidth = $(".orgchart-panel-container").width();
                                // scroll bar    min-width    .
                                // because ->        .
                                //     grid  resize  min-width  
                                $(".orgchart-page-container").css("min-width", pageContainerMinWidth + "px");

                                if (window.innerWidth < containerWidth) {
                                    $(".orgchart-page-container").css("min-width", containerWidth + "px");
                                }
                        }    
                        break;
                        
                    case PanelSplitMode.LeftGridRight:
                    default:
                        var gridWidth = $(".orgchart-panel-grid").width();
                        var splitterWidthTotal = $(".orgchart-splitter-left").width()
                        + $(".orgchart-splitter-right").width()
                        + 4; // splitter border 2px * 2(left, right);
                        var leftWidth = $(".orgchart-panel-left").width();
                        var rightWidth = $(".orgchart-panel-right").width();
                        var pageContainerMinWidth = gridPanelMinWidth
                        + splitterWidthTotal
                        + leftWidth
                        + rightWidth;

                        var containerWidth = $(".orgchart-panel-container").width();
                        // scroll bar    min-width    .
                        // because ->        .
                        //     grid  resize  min-width  
                        $(".orgchart-page-container").css("min-width", pageContainerMinWidth + "px");
                        if (window.innerWidth < containerWidth) {
                            $(".orgchart-page-container").css("min-width", containerWidth + "px");
                        }    
                        break;
                }
                
            }

            //   
            function stopDragging(e) {
                if (SplitPanelRefresh == false) {
                    //  refresh   
                    $handleShadow.css("display", "none");
                    refreshPanelForSplitter(e);
                }
                else {

                }

                // 2016-04-26 daddygom, page-container-width : min-width 
                // scrollbar  .
                setPageContainerMinWidth();
                // **************************************************
                // 2016-04-25 daddygom, grid panel set width ********
                //  .      
                setGridPanelWidth();
                // **************************************************

                // 2016-05-03 daddygom, calling sync scroll *********
                // 2016-05-27     .
                // 2016-06-10 dadygom, grid     syncScroll 
                if(panelSplitMode == PanelSplitMode.LeftGrid || panelSplitMode == PanelSplitMode.LeftGridRight)
                {
                    AppManager.controls.grid.syncScroll();
                    if (AppManager.controls.review != null) {
                        AppManager.controls.to.syncScroll();
                        AppManager.controls.cc.syncScroll();
                        AppManager.controls.review.syncScroll();
                    }
                }
                // **************************************************

                e.stopPropagation();
                e.preventDefault();

                $(document).unbind('mousemove.rsz', opt.dragFunc);
                $(document).unbind('mouseup.rsz', stopDragging);

                if (window.Touch || navigator.maxTouchPoints) {
                    $(document).unbind('touchmove.rsz', opt.dragFunc);
                    $(document).unbind('touchend.rsz', stopDragging);
                }
                $(document).unbind('selectstart.rsz', noop);

                // reset changed values
                $el.css("transition", startTransition);

                if (opt.onDragEnd) {
                    opt.onDragEnd(e, $el, opt);
                }

                return false;
            }

            function getMousePos(e) {
                var pos = { x: 0, y: 0, width: 0, height: 0 };
                if (typeof e.clientX === "number") {
                    pos.x = e.clientX;
                    pos.y = e.clientY;
                } else if (e.originalEvent.touches) {
                    pos.x = e.originalEvent.touches[0].clientX;
                    pos.y = e.originalEvent.touches[0].clientY;
                } else
                    return null;

                return pos;
            }
        });
    };
    
})(jQuery, undefined);



