var logger = require('loglevel');
var $ = require('jquery');
import { blockTooltip, releaseTooltip } from './tooltip-extend';

class PresenceManager {
  constructor() {
    this.nameCtrl = null;
    this.presences = [];
    this.enabled = false;

    // for test
    window.OnStatusChange = this.onStatusChange.bind(this);
  }

  initialize() {
    //       
    if (typeof window.AppConfig.presenceEnable === typeof undefined || window.AppConfig.presenceEnable === false) {
      return;
    }

    if(window.ActiveXObject || 'ActiveXObject' in window) {
      try {
        this.nameCtrl = new ActiveXObject("Name.NameCtrl.1");
      }
      catch (e) {
        logger.error('Can not create activeX object, %O', e);
      }
    } else {
      try {
        this.nameCtrl = new ActiveXObject("Name.NameCtrl.1");
      } catch (e) {
        logger.error('failed to create instance of Name.NameCtrl');
        return;
      }
    }

    // test
    try {
      this.nameCtrl.getStatus('', '');
    }
    catch (e) {
      logger.error('Can not call getStatus -> %O', e);
      this.nameCtrl = null;
    }

    try {
      if (this.nameCtrl /*&& this.nameCtrl.PresenceEnabled*/) {
        this.nameCtrl.OnStatusChange = this.onStatusChange.bind(this);
        logger.info('Presence install complete');
        this.enabled = true;
      }
      else {
        logger.info('Presence install fail');
      }
    }
    catch (e) {
      logger.error('failed bind to Name.NameCtrl.1 status callback function');
    }

    if (this.isEnabled()) {
      if (Org.config.useNameCard) {
        $('#orgGrid')
        .on('mouseover', 'span.status_access', function(e) {
          var $this = $(e.target);
          if (!$this.hasClass('status_hidden')) {
            var unique = $this.parents('tr')[0].getAttribute('data-unique');
            if (typeof unique === typeof undefined || unique === null) {
              return;
            }

            var item = AppManager.controls.grid.getItemByUnique(unique);

            var oouiX = 10;
            var oouiY = 10;

            // 2010    -  2013    
            {
              var objRet = this.PresenceControlGetOOUILocation($this[0]);
              var oouiX = objRet.oouiX;
              var oouiY = objRet.oouiY - 10;
            }

            this.nameCtrl.ShowOOUI(item.data.EmailAddress, 0, oouiX, oouiY);
            blockTooltip();
          }
        }.bind(this))
        .on('mouseout', 'span.status_access', function(e) {
          this.nameCtrl.HideOOUI();
          releaseTooltip();
        }.bind(this));
      }
    }
  }

  // 2010  OOUI  
  PresenceControlGetOOUILocation(obj) {
      var objRet = new Object;
      var objSpan = obj;
      var objOOUI = obj;
      var oouiX = 0, oouiY = 0, objDX = 0;
      var scrollTopY = (document.documentElement && document.documentElement.scrollTop) ? document.documentElement.scrollTop : (document.body.scrollTop ? document.body.scrollTop : 0);
      var fRtl = document.dir == "rtl";

      while (objSpan && objSpan.tagName != "SPAN" && objSpan.tagName != "TABLE") {
          objSpan = objSpan.parentNode;
      }

      if (objSpan) {
          var collNodes = objSpan.tagName == "TABLE" ? objSpan.rows(0).cells(0).childNodes : objSpan.childNodes;
          var i;
          for (i = 0; i < collNodes.length; ++i) {
              if (collNodes.item(i).tagName == "IMG" && collNodes.item(i).id) {
                  objOOUI = collNodes.item(i);
                  break;
              }

              if (collNodes.item(i).tagName == "A" &&
                      collNodes.item(i).childNodes.length > 0 &&
                      collNodes.item(i).childNodes.item(0).tagName == "IMG" &&
                      collNodes.item(i).childNodes.item(0).id) {
                  objOOUI = collNodes.item(i).childNodes.item(0);
                  break;
              }
          }
      }

      obj = objOOUI;
      while (obj) {
          if (fRtl) {
              if (obj.scrollWidth >= obj.clientWidth + obj.scrollLeft)
                  objDX = obj.scrollWidth - obj.clientWidth - obj.scrollLeft;
              else
                  objDX = obj.clientWidth + obj.scrollLeft - obj.scrollWidth;

              oouiX += obj.offsetLeft + objDX;
          }

          else
              oouiX += obj.offsetLeft - obj.scrollLeft;

          // Changed  Fixed scroll up coordination problem due to master page
          //oouiY+=obj.offsetTop - obj.scrollTop;
          oouiY += obj.offsetTop;
          obj = obj.offsetParent;

      }

      try {
          obj = window.frameElement;
          while (obj) {
              if (fRtl) {
                  if (obj.scrollWidth >= obj.clientWidth + obj.scrollLeft)
                      objDX = obj.scrollWidth - obj.clientWidth - obj.scrollLeft;
                  else
                      objDX = obj.clientWidth + obj.scrollLeft - obj.scrollWidth;
                  oouiX += obj.offsetLeft + objDX;
              }

              else
                  oouiX += obj.offsetLeft - obj.scrollLeft;

              // Changed  Fixed scroll up coordination problem due to master page
              // oouiY+=obj.offsetTop -
              obj.scrollTop;
              oouiY += obj.offsetTop;
              obj = obj.offsetParent;
          }

      } catch (e) {

      };

      objRet.objSpan = objSpan;
      objRet.objOOUI = objOOUI;
      objRet.oouiX = oouiX;
      //!!! This is the code change:
      // Changed  Fixed scroll up coordination problem due to master page
      //objRet.oouiY=oouiY
      objRet.oouiY = oouiY - scrollTopY;

      if (fRtl)
          objRet.oouiX += objOOUI.offsetWidth;

      return objRet;
  }

  isEnabled() {
    return !!(this.enabled === true && this.nameCtrl /*&& this.nameCtrl.PresenceEnabled;*/);
  }

  getStatusClass(state) {
    switch (state) {
      case 0:
      // available
      return 'status_access status_online';
      case 1:
      // offline
      return 'status_access status_offline';
      case 2:
      case 4:
      case 16:
      // away
      //return 'away';
      return 'status_access status_return';
      case 3:
      case 5:
      // inacall
      //return 'inacall';
      case 6:
      case 7:
      case 8:
      // outofoffice
      //return 'outofoffice';
      case 10:
      // busy
      case 9:
      return 'status_access status_busy';
      case 15:
      return 'status_access status_offline';
    }
  }

  updateStatus(name, status) {
    var items = AppManager.controls.grid.state.items;
    for (var i = 0; i < items.length; i++) {
      if (items[i].data.EmailAddress === name) {
        for (var j = 0; j < items[i].cells.length; j++) {
          if (items[i].cells[j].columnName === 'name') {
            items[i].cells[j].iconClass = this.getStatusClass(status);

            AppManager.controls.grid.setState(AppManager.controls.grid.state);
            return;
          }
        }
      }
    }
  }

  onStatusChange(name, status, id) {
    this.updateStatus(name, status);
  }

  getStatus(name, entryType) {
    if (!this.isEnabled()) return '';
    if (entryType !== 2) {
      return 'status_access status_hidden';
    }
    var status = this.nameCtrl.GetStatus(name, '');
    return this.getStatusClass(status);
  }
}

export default new PresenceManager();



