export default {
  get: function(lang, path) {
    if (lang === 'cn') {
      lang = 'zh';
    }
    var langObj = _.get(resource, lang, null);
    if (langObj !== null) {
      var txt = _.get(langObj, path, null);
      return txt;
    }
    return null;
  },
  merge: function(o) {
    if ('cn' in o) {
      o.zh = o.cn
    }
    if (o !== 'undefined') {
      _.merge(resource, Org.resource);
    }
  }
}



