var $ = require('jquery');

var $spinner = $('.spinner');

export default {
  show: function() {
    $('.pop_wrap > .bg').css('background-color', 'rgba(255, 255, 255, .05)');
    $('.pop_wrap').fadeIn(200);
    $spinner.fadeIn(200);
  },

  hide: function(delayed) {
    if (typeof delayed === typeof undefined || delayed === true) {
      $('.pop_wrap').fadeOut(200);
      $spinner.fadeOut(200);
    }
    else if (delayed === false) {
      $('.pop_wrap').hide();
      $spinner.hide();
    }
  },

  close: function() {
    $('.pop_wrap').hide();
    $spinner.hide();
  }
}



