var OrgExtension = window.OrgExtension || {};

function getExtensionBodyJson() {
  var body = {};

  if (OrgExtension.SpContext) {
    body.SpContext = JSON.stringify(OrgExtension.SpContext);
  }
  if (OrgExtension.SpExtParamContext) {
    body.SpExtParamContext = JSON.stringify(OrgExtension.SpExtParamContext);
  }

  return body;
}

module.exports = {
  getExtensionBody: function() {
    var body = getExtensionBodyJson();

    return JSON.stringify(body);
  },
  getDeptExtensionBody: function() {
    var body = getExtensionBodyJson();

    if (OrgExtension.InitDeptContext) {
      body.InitDeptContext = JSON.stringify(OrgExtension.InitDeptContext);
    }

    return JSON.stringify(body);
  },
  getOrgExtension: function() {
    return OrgExtension;
  }
}



