import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');

class ListHeader extends React.Component {
  /**
  * @typedef {{onChecked: function, checked: boolean, title: string}} HeaderProperty
  */
  /**
  * constructor
  * @param  {HeaderProperty} props property
  */
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange() {
    this.props.onHeaderUpdate(this.refs.checkbox.checked);
  }

  render() {
    return (
      <div className="tbl_vert">
        <table>
          <colgroup>
            <col width="30"></col>
            <col></col>
          </colgroup>
          <thead>
            <tr className="tbl_row">
              <th className="tbl_head" colSpan="2">{this.props.title}</th>
            </tr>
          </thead>
        </table>
      </div>
    );
  }
}

export default ListHeader;



