import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');
import ListHeader from './list-header.jsx';
import ListItem from './list-item.jsx';
import Guid from 'guid';

class List extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      header: {
        title: '',
        checked: false,
        selectAll: false,
        onHeaderUpdate: this.onHeaderUpdate.bind(this)
      },
      items: []
    };

    this.onHeaderUpdate = this.onHeaderUpdate.bind(this);
    this.onItemUpdate = this.onItemUpdate.bind(this);
    this.onSingleSelection = this.onSingleSelection.bind(this);
    this.onToggleSelection = this.onToggleSelection.bind(this);
    this.onRangeSelection = this.onRangeSelection.bind(this);
    this.onSelectAll = this.onSelectAll.bind(this);

    this.selectCache = null;
  }

  onHeaderUpdate(checked) {
    this.setHeader({checked: checked});
  }

  onSingleSelection(e) {
    this.selectCache = e.props.item;

    for (var i = 0; i < this.state.items.length; i++) {
      this.state.items[i].checked = false;
    }
    e.props.item.checked = true;
    this.state.selectAll = this.isSelectAll();
    this.setState(this.state);
  }

  onToggleSelection(e) {
    e.props.item.checked = !e.props.item.checked;

    if (e.props.item.checked) {
      this.selectCache = e.props.item;
    }
    this.state.selectAll = this.isSelectAll();
    this.setState(this.state);
  }

  onRangeSelection(e) {
    if (this.selectCache === null) {
      for (var i = 0; i < this.state.items.length; i++) {
        if (this.state.items[i] != e.props.item) {
          this.state.items[i].checked = true;
        }
        else {
          this.state.items[i].checked = true;
          this.selectCache = this.state.items[i];
          break;
        }
      }

      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
    else {
      // basic validation
      if (e.props.item === this.selectCache) return;

      var inRange = false;
      for (var i = 0; i < this.state.items.length; i++) {
        if (inRange === false) {
          if (this.state.items[i] === this.selectCache || this.state.items[i] === e.props.item) {
            inRange = true;
          }
        }
        else {
          if (this.state.items[i] === this.selectCache || this.state.items[i] === e.props.item) {
            inRange = false;
            this.state.items[i].checked = true;
            continue;
          }
        }

        if (inRange === true) {
          this.state.items[i].checked = true;
        }
        else {
          this.state.items[i].checked = false;
        }
      }

      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
  }

  onItemUpdate(unique, checked) {
    //for (var item of this.state.items) {
    for (var i = 0; i < this.state.items.length; i++) {
      var item = this.state.items[i];
      if (item.unique === unique) {
        item.checked = checked;

        this.state.selectAll = this.isSelectAll();
        this.setState(this.state);

        return;
      }
    }
  }

  /**
  * @typedef {{title: string, checked: boolean}} HeaderState
  */
  /**
  * header state change
  * @param {HeaderState} header change state
  */
  setHeader(header) {
    var title = _.get(header, 'title', this.state.header.title);
    var checked = _.get(header, 'checked', this.state.header.checked);

    var state = update(this.state, {
      header: {
        title: {$set: title},
        checked: {$set: checked}
      }
    });
    this.setState(state);
  }

  /**
  * @typedef {{unique: any, text: string, checked: boolean, data: Object}} ListItem
  */
  /**
  * add list item
  * @param {ListItem} item add item
  * @param [ListItem] item add item array
  */
  addItem(item) {
    if (Array.isArray(item)) {
      //for (var i of item) {
      for (var ix = 0; ix < item.length; ix++) {
        var i = item[ix];
        this.addItem(i);
      }
    }
    else {
      //for (var current of this.state.items) {
      for (var i = 0; i < this.state.items.length; i++) {
        var current = this.state.items[i];
        if (current.unique === item.unique) {
          return;
        }
      }

      var text = _.get(item, 'text', '');
      var checked = _.get(item, 'checked', false);
      var data = _.get(item, 'data', null);
      var unique = _.get(item, 'unique', Guid.v4());
      var selected = _.get(item, 'selected', false);

      var itemState = {
        unique: unique,
        text: text,
        checked: checked,
        data: data,
        selected: selected,
        onItemUpdate: this.onItemUpdate
      };

      this.state.items.push(itemState);
      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
  }

  removeItem(unique) {
    if (Array.isArray(unique)) {
      //for (var u of unique) {
      for (var i = 0; i < unique.length; i++) {
        var u = unique[i];
        this.removeItem(u);
      }
    }
    else {
      var itemLength = this.state.items.length;
      for (var i = 0; i < itemLength; i++) {
        if (this.state.items[i].unique === unique) {
          this.state.items.splice(i, 1);

          break;;
        }
      }

      this.state.selectAll = this.isSelectAll();
      this.setState(this.state);
    }
  }

  getCheckedItems() {
    var items = [];
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].checked === true) {
        items.push(this.state.items[i]);
      }
    }
    return items;
  }

  getItemByUnique(unique) {
    for (var i = 0; i < this.state.items.length; i++) {
      if (this.state.items[i].unique === unique) {
        return this.state.items[i];
      }
    }
    return null;
  }

  clear() {
    this.state.items = [];
    this.state.selectAll = this.isSelectAll();
    this.setState(this.state);
  }

  isSelectAll() {
    var length = this.state.items.length;
    if (length === 0) return false;
    for (var i = 0; i < length; i++) {
      if (this.state.items[i].checked === false) return false;
    }
    return true;
  }

  onSelectAll(selected) {
    this.state.selectAll = selected;
    for (var i = 0; i < this.state.items.length; i++) {
      this.state.items[i].checked = selected;
    }
    this.setState(this.state);
  }

  render() {
    var itemComponents = [];
    //for (var item of this.state.items) {
    for (var i = 0; i < this.state.items.length; i++) {
      var item = this.state.items[i];
      itemComponents.push(
        <ListItem key={item.unique}
                  onSingleSelection={this.onSingleSelection}
                  onToggleSelection={this.onToggleSelection}
                  onRangeSelection={this.onRangeSelection}
                  item={item}/>
      );
    }

    var tableStyle = {
      width: '100%',
    };
    var wrapStyle = {
      height: '100%',
      width: '100%',
      overflow: 'auto',
    };

    return (
      <div style={wrapStyle}>
        <div id={this.props.listId} className="tbl_vert">
          {this.props.showHeader ? <ListHeader {...this.state.header} /> : ""}
          <table style={tableStyle}>
            <colgroup>
              <col width="30"></col>
              <col></col>
            </colgroup>
            <tbody>
              {itemComponents}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default List;



