var $ = require('jquery');
var _ = require('lodash');

class StyledSelect {
  constructor(q, option) {
    this.$el = $(q);
    this.$dp = null;
    this.$op = null;
    this.readonly = false;

    if (typeof q === 'string') {
      this.$dp = $(q + ' > .select-styled');
      this.$op = $(q + ' > .select-options');
    }
    else {
      this.$dp = this.$el.children('.select-styled');
      this.$op = this.$el.children('.select-options');
    }

    this.changes = [];
    this.current = '';
    this.placeholder = '';
    if (_.has(option, 'placeholder')) {
      this.placeholder = option.placeholder;
    }
    if (_.has(option, 'readonly') && typeof option.readonly === 'boolean') {
      this.readonly = option.readonly;
    }
    if (this.readonly === true) {
      this.$dp.addClass('readonly');
    }
    this.$dp.addClass('empty');
    this.$dp.text(this.placeholder);

    if (!this.readonly) {
      this.$dp.click(function(e) {
        if (this.$dp.hasClass('active')) {
          this.$dp.removeClass('active');
          this.$op.slideUp(200);
        }
        else {
          this.$dp.addClass('active');
          this.$op.slideDown(200);
        }
        e.stopPropagation();
      }.bind(this));
    }

    if (!this.readonly) {
      this.$op.on('click', 'li', function(e) {
        e.stopPropagation();
        this.$dp.removeClass('active');
        this.$dp.removeClass('empty');
        this.$op.slideUp(200);

        this.$dp.text(e.target.innerText);
        if (this.current !== e.target.getAttribute('data-value')) {
          this.current = e.target.getAttribute('data-value');
          this.$dp.attr('data-value', this.current);
          for (var i = 0; i < this.changes.length; i++) {
            this.changes[i]({
              text: e.target.innerText,
              value: e.target.getAttribute('data-value'),
            });
          }
        }
      }.bind(this));
    }

    $(document).click(function() {
      this.$dp.removeClass('active');
      this.$op.slideUp(200);
    }.bind(this));
  }

  static create(mount, option) {
    var content = ``;
    var containerId = _.has(option, 'container.id') ? "id=\"" + option.container.id + "\"" : "";
    var containerStyle = _.has(option, 'container.style') ? option.container.style : "";
    var containerClass = _.has(option, 'container.className') ? option.container.className : "";
    content += `<div ${containerId} class="${containerClass}" style="${containerStyle}">`;
    var selectId = _.has(option, 'select.id') ? "id=\"" + option.select.id + "\"" : "";
    content += `  <div ${selectId} class="select" style="width: 100%;">`;
    var isArrow = _.has(option, 'select.arrow') ? option.select.arrow : true;
    content += `    <div class="select-styled ${isArrow ? 'arrow' : ''}"></div>`;
    content += `    <ul class="select-options" style="max-height: 300px; overflow: auto;">`;
    content += `    </ul>`;
    content += `  </div>`;
    content += `</div>`;

    var direction = _.has(option, 'direction') ? option.direction : 'append';
    var $el = null;
    if (direction === 'append') {
      $el = $(content).appendTo($(mount));
    }
    else if (direction === 'prepend') {
      $el = $(content).prependTo($(mount));
    }
    else if (direction === 'before') {
      $el = $(content).insertBefore($(mount));
    }
    else if (direction === 'after') {
      $el = $(content).insertAfter($(mount));
    }

    if (selectId !== '') {
      return new StyledSelect('#' + selectId, option);
    }
    else {
      return new StyledSelect($el.children('.select'), option);
    }
  }

  change(fn) {
    this.changes.push(fn);
  }

  reset() {
    this.$dp.removeClass('active');
    this.$op.slideUp(200);

    if (this.current !== '') {
      this.$dp.addClass('empty');
      this.$dp.text(this.placeholder);
      this.current = '';
      for (var i = 0; i < this.changes.length; i++) {
        this.changes[i]({
          text: '',
          value: '',
        });
      }
    }
  }

  clear() {
    this.reset();
    this.$op.empty();
  }

  setPlaceHolder(placeholder) {
    this.placeholder = placeholder;
    if (this.current === '') {
      this.$dp.text(this.placeholder);
    }
  }

  addOption(o) {
    if (Array.isArray(o)) {
      for (var i = 0; i < o.length; i++) {
        this.addOption(o[i]);
      }
    }
    else {
      if (typeof o === 'string') {
        var option = {
          text: o
        };
        option['data-value'] = o;
        $('<li />', option).appendTo(this.$op);
      }
      else {
        var option = {
          text: o.text
        };
        option['data-value'] = o.value;
        $('<li />', option).appendTo(this.$op);

        if (_.has(o, 'selected')) {
          if (o.selected === true) {
            this.$dp.removeClass('empty');
            this.$dp.text(o.text);
            this.current = o.value;

            for (var i = 0; i < this.changes.length; i++) {
              this.changes[i]({
                text: o.text,
                value: o.value,
              });
            }
          }
        }
      }
    }
  }

  getElementByValue(val) {
    var li = this.$op.children();
    for (var i = 0; i < li.length; i++) {
      var dataValue = li[i].getAttribute('data-value');
      if (dataValue === val) {
        return li[i];
      }
    }
    return null;
  }
}

