    this.paging.controls.current = document.querySelector('#input-current-page');
    this.paging.controls.total = document.querySelector('#txt-total-page');
    //paging event
    this.paging.controls.current.addEventListener('keydown', function(e) {
      // allow only numbers
      if (e.keyCode === 46 || e.keyCode === 8 || e.keyCode === 9 || e.keyCode === 27 || e.keyCode === 13
        || e.keyCode === 35 || e.keyCode === 36 || e.keyCode === 39 || e.keyCode === 37
        || (e.keyCode >= 48 && e.keyCode <= 57)) {
        if (e.keyCode === 13) {
          var page = parseInt(this.paging.controls.current.value, 10);
          if (Number.isNaN(page)) {
            this.paging.controls.current.value = this.paging.currentPage;
            return;
          }
          this.paging.currentPage = page;
          if (this.paging.currentPage > this.paging.totalPage) {
            this.paging.currentPage = this.paging.totalPage;
          }
          else if (this.paging.currentPage < 1) {
            this.paging.currentPage = 1;
          }

          if (this.viewMode === 'dept') {
            this.fetchDeptGridData();
          }
          else if (this.viewMode === 'contact') {
            this.fetchContactGridData();
          }
          else if (this.viewMode === 'search') {
            this.search(this.searchInfo.keyword);
          }
        }
      }
      else {
        e.preventDefault();
        return false;
      }
    }.bind(this));
