    // document.querySelector('#radio01').addEventListener('click', function(e) {
    //   this.setViewMode('dept');
    // }.bind(this));
    // document.querySelector('#radio02').addEventListener('click', function(e) {
    //   this.setViewMode('contact');
    // }.bind(this));

    // search event
    document.querySelector('#btn-search').addEventListener('click', function(e) {
      e.preventDefault();
      this.setViewMode('search');
      this.search(document.querySelector('#input-search').value);
    }.bind(this));
    document.querySelector('#input-search').addEventListener('keydown', function(e) {
      if (e.keyCode === 13) {
        this.setViewMode('search');
        this.search(e.target.value);
      }
    }.bind(this));

    // // AppConfig.info
    // var settings = window.AppConfig.info.Settings;
    // var companies = window.AppConfig.info.Companies;
    //
    // // set my info
    // this.userInfo.companyCode = settings.CompanyCode;
    // this.userInfo.childCompanyCode = settings.ChildCompanyCode;
    // this.userInfo.deptCode = settings.DeptCode;
    // this.userInfo.emailAddress = settings.EmailAddress;

    // language event bind
    var ul = document.querySelector('#ul-language-slide');
    
    if (window.AppConfig.info.Settings.IsEnglish === true) {
      this.multiLang.push('en');

      // dynamic html injection
      var txt = document.createTextNode('ENG');

      var a = document.createElement('a');
      a.className = 'lang_anchor';
      a.setAttribute('href', '#');
      a.appendChild(txt);

      var li = document.createElement('li');
      li.className = 'lang_item';
      li.id = 'li-language-en';
      li.appendChild(a);

      ul.appendChild(li);

      a.addEventListener('click', function(e) {
        e.preventDefault();
        $('#ul-language-slide').slideUp(200, function() {
          this.setLanguage('en');
        }.bind(this));
      }.bind(this));
    }
    if (window.AppConfig.info.Settings.IsChiness === true) {
      this.multiLang.push('zh');

      // dynamic html injection
      var txt = document.createTextNode('CHN');

      var a = document.createElement('a');
      a.className = 'lang_anchor';
      a.setAttribute('href', '#');
      a.appendChild(txt);

      var li = document.createElement('li');
      li.className = 'lang_item';
      li.id = 'li-language-zh';
      li.appendChild(a);

      ul.appendChild(li);

      a.addEventListener('click', function(e) {
        e.preventDefault();
        $('#ul-language-slide').slideUp(200, function() {
          this.setLanguage('zh');
        }.bind(this));
      }.bind(this));
    }
	if (this.multiLang.length == 0 || window.AppConfig.info.Settings.IsKorean === true) {
      this.multiLang.push('ko');

      // dynamic html injection
      var txt = document.createTextNode('KOR');

      var a = document.createElement('a');
      a.className = 'lang_anchor';
      a.setAttribute('href', '#');
      a.appendChild(txt);

      var li = document.createElement('li');
      li.className = 'lang_item';
      li.id = 'li-language-ko';
      li.appendChild(a);

      ul.appendChild(li);

      a.addEventListener('click', function(e) {
        e.preventDefault();
        $('#ul-language-slide').slideUp(200, function() {
          this.setLanguage('ko');
        }.bind(this));
      }.bind(this));
    }
	
    var findInMultiLang = false;
    for (var i = 0; i < this.multiLang.length; i++) {
      if (this.multiLang[i] == this.language) {
        findInMultiLang = true;
        break;
      }
    }
    if (!findInMultiLang) {
      var findEnMultiLang = false;
      for (var i = 0; i < this.multiLang.length; i++) {
        if (this.multiLang[i] == 'en') {
          this.language = 'en';
          findEnMultiLang = true;
          break;
        }
      }

      if (!findEnMultiLang) {
        this.language = this.multiLang[0];
      }
    }

