      var option = {
        text: window.AppConfig.info.Companies.Items[i].DisplayName,
        value: window.AppConfig.info.Companies.Items[i].CompanyCode
      };
      if (window.AppConfig.info.Companies.Items[i].CompanyCode === this.userInfo.companyCode) {
        option.selected = true;
        // $('#txt_company').text(option.text);
        this.companyCode = option.value;

        Org.invoke('companySelect', option);
      }
      this.controls.companySelect.addOption(option);
