class GridItemCell {
  constructor() {
    this.unique = null;
    this.text = '';
    this.html = null;
    this.columnName = '';
    this.data = null;
    this.minWidth = 0;
    this.maxWidth = 0;
    this.width = 100;
    this.useIcon = false;
    this.iconClass = '';
  }
}

export default GridItemCell;



