import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');
import TreeItem from './tree-item.jsx';
import TreeNode from './tree-node';
import fetch from 'fetch/fetch';
import 'whatwg-fetch';
import Guid from 'guid';

var $ = require('jquery');

if (typeof Promise === typeof undefined) {
  var Promise = require('es6-promise/es6-promise');
}

class Tree extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // selectCallback: null,
      root: null,
      fetcher: null,
    };
    this.onSelect = this.onSelect.bind(this);
    this.onNodeChange = this.onNodeChange.bind(this);
    this.onFetch = this.onFetch.bind(this);
    this.onNodeMouseMove = this.onNodeMouseMove.bind(this);

    this.selected = null;
    this.selectedNode = null;
    this.mouseOver = null;

    this.events = {};
  }

  invoke(event) {
    var ev = this.events[event];
    if (typeof ev === typeof undefined || ev === null) {
      logger.warn(`${event} is not exists in event list`);
      return;
    }

    var params = Array.prototype.slice.call(arguments, 1);

    for (var i = 0; i < ev.length; i++) {
      if (params.length === 0) {
        ev[i]();
      }
      else {
        ev[i].apply(null, params);
      }
    }
  }

  on(event, fn) {
    if (typeof fn !== 'function') {
      logger.warn(`${event} is a function [event list]`);
      return;
    }

    var eventArr = event.trim().split(' ');
    var ev = [];
    for (var i = 0; i < eventArr.length; i++) {
      var t = eventArr[i].trim();
      if (t === '' || t === ' ') {
        continue;
      }
      ev.push(t);
    }

    for (var i = 0; i < ev.length; i++) {
      var e = ev[i];
      if (!_.has(this.events, e)) {
        this.events[e] = [];
      }
      this.events[e].push(fn);
    }
  }

  clear() {
    this.mouseOver = null;
    this.state.root = null;
    this.setState(this.state);
  }

  /**
   * add node
   * @param {{
   *   unique: Object,
   *   text: string,
   *   hasChild: boolean,
   *   expanded: boolean,
   *   data: Object
   * }} node   node
   * @param {TreeNode} parent parent node
   */
  addNode(node, parent) {
    var nodeObject = new TreeNode();

    if (typeof node === 'string') {
      nodeObject.unique = Guid.v4();
      nodeObject.text = node;
      nodeObject.hasChild = _.get(node, 'hasChild', false);
      nodeObject.expanded = _.get(node, 'expanded', false);
      nodeObject.data = null;
    }
    else {
      nodeObject.unique = _.get(node, 'unique', Guid.v4());
      nodeObject.text = node.text;
      nodeObject.hasChild = _.get(node, 'hasChild', false);
      nodeObject.expanded = _.get(node, 'expanded', false);
      nodeObject.data = _.get(node, 'data', null);
      nodeObject.selected = _.get(node, 'selected', false);
      nodeObject.iconClass = _.get(node, 'iconClass', nodeObject.iconClass);
    }

    if (typeof parent !== typeof undefined && parent !== null) {
      parent.addChild(nodeObject);
      parent.hasChild = true;
    }
    else {
      if (this.state.root === null) {
        this.state.root = nodeObject;
      }
      else {
        this.state.root.addSibling(nodeObject);
      }
    }

    if (nodeObject.selected) {
      // if (this.selected === nodeObject.unique) return;
      // this.selected = nodeObject.unique;
      //
      // //var unique = e.props.unique;
      // var text = nodeObject.text;
      // var data = nodeObject.data;
      //
      // if (this.state.selectCallback !== null) {
      //   this.state.selectCallback(text, data);
      // }
      this.setSelected(nodeObject);
    }
    else {
      this.setState(this.state);
    }

    return nodeObject;
  }

  addNodes(nodes, parent) {
    var nodeObject = null;

    if (Array.isArray(nodes)) {
      //for (var node of nodes) {
      for (var i = 0; i < nodes.length; i++) {
        var node = nodes[i];
        if (Array.isArray(node)) {
          this.addNodes(node, nodeObject);
        }
        else {
          nodeObject = this.addNode(node, parent);
        }
      }
    }
    else {
      this.addNode(nodes, parent);
    }
  }

  getNodeByUnique(unique) {
    var findNode = function(node, findSet) {
      var target = node;
      while (target !== null) {
        if (target.unique === findSet.unique) {
          findSet.node = target;
          return true;
        }

        if (target.childNode !== null) {
          var result = findNode(target.childNode, findSet);
          if (result === true) return true;
        }

        target = target.nextNode;
      }

      return false;
    }

    var findSet = {
      unique: unique,
      node: null,
    };
    var find = findNode(this.state.root, findSet);
    if (find) {
      return findSet.node;
    }
    else {
      return findSet.node;
    }
  }

  // setSelectCallback(callback) {
  //   this.state.selectCallback = callback;
  // }

  setSelectedByUnique(unique) {
    var node = this.getNodeByUnique(unique);
    return this.setSelected(node);
  }

  setSelected(node) {
    if (this.selectedNode !== null) {
      this.selectedNode.selected = false;
    }

    if (node !== null) {
      node.selected = true;
    }
    var selectNodeChange = false;
    if (this.selectedNode !== node) {
      selectNodeChange = true;
    }
    this.selectedNode = node;

    this.setState(this.state);

    // if (this.state.selectCallback !== null && node !== null) {
    //   this.state.selectCallback(node.text, node.data);
    // }
    if (node !== null) {
      if (selectNodeChange) {
        this.invoke('selectChange', node.text, node.data);
      }
    }

    return node;
  }

  onSelect(node) {
    this.setSelected(node);
    // if (this.selected === e.props.node.unique) return;
    // this.selected = e.props.node.unique;
    //
    // //var unique = e.props.unique;
    // var text = e.props.node.text;
    // var data = e.props.node.data;
    //
    // if (this.state.selectCallback !== null) {
    //   this.state.selectCallback(text, data);
    // }
  }

  unselect() {
    this.setSelected(null);
  }

  openNode(node, fn) {
    var target = node;
    while (target !== null) {
      if (target.childNode !== null) {
        target.expanded = true;
      }

      target = target.findParent();
    }

    this.setState(this.state);
	
	if (typeof fn !== 'undefined') {
		fn(node);
	}
  }

  onNodeMouseMove(e) {
    this.mouseOver = e.props.node;
  }

  getMouseOverItem() {
    return this.mouseOver;
  }

  getSelectedItem() {
    var item = this.selectedNode;
    return item;
  }

  onNodeChange(e) {
    this.setState(this.state);
  }

  // fetcher object
  // {
  //   url: '',
  //   option: Object,
  //   getUrl: function(data) { return ''; },
  //   getItem: function(e) { return null; }
  // }
  setFetcher(fetcher) {
    this.state.fetcher = fetcher;
  }

  onFetch(e) {
    var url = '';
    if (typeof this.state.fetcher.url !== typeof undefined && this.state.fetcher.url !== null) {
      url = this.state.fetcher.url;
    }
    else if (typeof this.state.fetcher.getUrl !== typeof undefined && this.state.fetcher.getUrl !== null) {
      url = this.state.fetcher.getUrl(e.props.node.data);
    }

    if (typeof this.state.fetcher.onBefore !== typeof undefined) {
      this.state.fetcher.onBefore();
    }

    var ajaxOption = this.state.fetcher.option;
    ajaxOption.url = url;

    $.ajax(ajaxOption)
    .done(function(j) {
      if (typeof this.state.fetcher.onSuccess !== typeof undefined) {
        var item = this.state.fetcher.onSuccess(j);
        this.addNodes(item, e.props.node);
        e.props.node.expanded = !e.props.node.expanded;
        this.onNodeChange(e);
      }
    }.bind(this))
    .fail(function(err) {
      if (typeof this.state.fetcher.onFail !== typeof undefined) {
        this.state.fetcher.onFail(err);
      }
    }.bind(this));

    //window.fetch(url, this.state.fetcher.option)
    //.then(function(response) {
    //  if (response.ok) {
    //    return response.json();
    //  }
    //  else {
    //    throw response;
    //  }
    //}.bind(this))
    //.then(function(j) {
    //  if (typeof this.state.fetcher.onSuccess !== typeof undefined) {
    //    var item = this.state.fetcher.onSuccess(j);
    //    this.addNodes(item, e.props.node);
    //    e.props.node.expanded = !e.props.node.expanded;
    //    this.onNodeChange(e);
    //  }
    //}.bind(this))
    //.catch(function(err) {
    //  if (typeof this.state.fetcher.onFail !== typeof undefined) {
    //    this.state.fetcher.onFail(err);
    //  }
    //}.bind(this));
  }

  renderTravel() {
    if (this.state.root !== null) {
      var node = this.state.root;
      var nodeComponent = [];
      while (node !== null) {
        nodeComponent.push(
          <TreeItem
            key={node.unique}
            node={node}
            onSelect={this.onSelect}
            onNodeChange={this.onNodeChange}
            fetcher={this.state.fetcher}
            onFetch={this.onFetch}
            onNodeMouseMove={this.onNodeMouseMove}
            />
        );
        node = node.nextNode;
      }
      return nodeComponent;
    }
    return null;
  }

  componentDidMount() {
    var container = this.refs.container;
    $(container).parent().scroll(function() {
      container.style.width = this.scrollWidth + 'px';
    });
  }

  render() {
    return (
      <div ref="container" className="tree" draggable="false" style={{minWidth: '100%'}}>
        <ul className="tree_list" draggable="false">
          {this.renderTravel()}
        </ul>
      </div>
    );
  }
}

export default Tree;



