import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');
var VelocityTransitionGroup = require('velocity-react/velocity-transition-group');
require('velocity-animate');
require('velocity-animate/velocity.ui');

class TreeItem extends React.Component {
  constructor(props) {
    super(props);
    this.onNodeClick = this.onNodeClick.bind(this);
    this.onExpandChange = this.onExpandChange.bind(this);
    this.onNodeMouseMove = this.onNodeMouseMove.bind(this);
    //this.scrollIntoView = this.scrollIntoView.bind(this);
  }

  onNodeClick(e) {
    e.preventDefault();
    this.props.onSelect(this.props.node);
  }

  onExpandChange(e) {
    //this.props.node.expanded = !this.props.node.expanded;

    if (this.props.node.expanded === false && this.props.node.childNode === null) {
      if (this.props.fetcher !== null) {
        this.props.onFetch(this);
      }
    }
    else {
      this.props.node.expanded = !this.props.node.expanded;
      this.props.onNodeChange(this);
    }

    //this.props.onNodeChange(this);
  }

  onNodeMouseMove(e) {
    this.props.onNodeMouseMove(this);
  }

  renderIndent() {
    var indentSpan = [];
    for (var i = 1; i <= this.props.node.depth; i++) {
      if (i === this.props.node.depth &&
        (this.props.node.hasChild === true || this.props.node.childNode !== null)) {
        if (this.props.node.expanded === true) {
          indentSpan.push(
            <button key={this.props.node.unique + i}
                    className="toggle minus"
                    onClick={this.onExpandChange}></button>
          );
        }
        else {
          indentSpan.push(
            <button key={this.props.node.unique + i}
                    className="toggle plus"
                    onClick={this.onExpandChange}></button>
          );
        }
      }
      else {
        var n = this.props.node.depth - i;
        if (n === 0) {
          indentSpan.push(
            <span key={this.props.node.unique + i} className="tree_elbow"></span>
          );
        }
        else {
          var nthParent = this.props.node.getNthParent(n);
          if (nthParent.nextNode !== null) {
            indentSpan.push(
              <span key={this.props.node.unique + i} className="tree_line"></span>
            );
          }
          else {
            indentSpan.push(
              <span key={this.props.node.unique + i} className="tree_indent"></span>
            );
          }
        }

        // indentSpan.push(
        //   <span key={this.props.node.unique + i} className="tree_indent"></span>
        // );
      }
    }

    return indentSpan;
  }

  componentDidMount() {
    this.props.node.item = this.refs.item;
  }

  renderChild() {
    if (this.props.node.childNode !== null) {
      var node = this.props.node.childNode;
      var nodeComponent = [];
      while (node !== null) {
        nodeComponent.push(
          <TreeItem
            key={node.unique}
            node={node}
            fetcher={this.props.fetcher}
            onSelect={this.props.onSelect}
            onNodeChange={this.props.onNodeChange}
            onFetch={this.props.onFetch}
            onNodeMouseMove={this.props.onNodeMouseMove}
          />
        );
        node = node.nextNode;
      }

      var style = {};
      if (this.props.node.expanded === true) {
        style.display = 'block';
      }

      return (
        <ul className="tree_list" style={style}>
          {nodeComponent}
        </ul>
      );
    }
    return null;
  }

  render() {
    var visualAttrs = {};
    if (this.props.node.selected) {
      visualAttrs['data-selected'] = '';
    }

    return (
      <li className="tree_item no_selection" draggable="false">
        <div className="tree_item_wrap"
             ref="item"
             draggable="false"
             {...visualAttrs}
             onMouseMove={this.onNodeMouseMove}
             unselectable="on">
          {this.renderIndent()}
          <a href="#"
             className="tree_anchor"
             draggable="false"
             onClick={this.onNodeClick}
             unselectable="on">
            <span className={this.props.node.iconClass} unselectable="on"></span>
            <span className="txt" unselectable="on">{this.props.node.text}</span>
          </a>
        </div>
          {this.props.node.expanded ? this.renderChild() : undefined}
      </li>
    );
  }
}

export default TreeItem;



