import React from 'react';
var update = require('react-addons-update');
var _ = require('lodash');
import Guid from 'guid';

class GridHeader extends React.Component {
  constructor(props) {
    super(props);
    this.checkboxId = Guid.v4();
    this.onSelectAll = this.onSelectAll.bind(this);
  }

  onSelectAll() {
    this.props.onSelectAll(!this.props.selectAll);
  }

  renderColumns() {
    var columns = [];
    if (this.props.header !== null) {
      //for (var cell of this.props.header.cells) {
      for (var i = 0; i < this.props.header.cells.length; i++) {
        var cell = this.props.header.cells[i];
        columns.push(
          <th key={cell.unique} className="tbl_head">
            {cell.text}
          </th>
        );
      }
    }
    return columns;
  }

  render() {
    var checkAllStyle = {};
    if (this.props.showCheckAll === false) {
      checkAllStyle.display = 'none';
    }
    return (
      <tr className="tbl_row">
        <th className="tbl_head no_ellipsis">
          <input type="checkbox"
                 style={checkAllStyle}
                 checked={this.props.selectAll}
                 onClick={this.onSelectAll}
                 id={this.checkboxId} />
          <label htmlFor={this.checkboxId} style={checkAllStyle}>
            <span className="checkbox"></span>
          </label>
        </th>
        {this.renderColumns()}
      </tr>
    );
  }
}

export default GridHeader;



