// export to window-global module
import Org from './org-interface';
Org.multiLang = LangHelper;
if (typeof window.Org === typeof undefined) {
  window.Org = Org;
}

import OrgConfig from './org-config';
OrgConfig.initialize();
window.Org.config = OrgConfig;


// lazy loading -> initialize (jQuery)
$(function() {
  Org.invoke('config');

  if (Org.config.showToolbar === false) {
    var $toolbar = $('#pnl_toolbar');
    var $content = $('#pnl_content');

    $toolbar.hide();
    $content.css('height', `calc(100% - 91px)`);
  }

//ApplicationStartup(function() {
  OrgConfig.updateTitle();
  if (typeof window.Org.resource !== 'undefined') {
    LangHelper.merge(window.Org.resource);
  }

  // application layout setting
  if (OrgConfig.section === 'APP') {
    $('#pnl-list-to').hide();
    $('#pnl-list-cc').hide();
    $('#pnl-list-bcc').hide();
    $('#pnl-list-sel').show();

    if (OrgConfig.onlyOneSelect === true) {
      // hide list
      $('#pnl_content_list').css('width', '0').css('min-width', '0').hide();
      $('#resizer_list').css('width', '0').hide();
    }

    if (OrgConfig.appType === 'DEPT') {
      // hide grid

      $('#pnl_content_grid').css('width', '0').css('min-width', '0').hide();
      $('#resizer_list').css('width', '0').hide();
      var treeWidth = $('#pnl_content_tree').width();
      $('#pnl_content_list').css('width', `calc(100% - ${treeWidth + 5}px)`);

      // hide search
      $('#toggle_search_option').hide();
      $('#search-box').hide();

      if (OrgConfig.onlyOneSelect === false) {
        // &
        require('./resize-extend');
        // $(".orgchart-panel-left").resizable_left_splitter({
        //    handleSelector: ".orgchart-splitter-left",
        //    handleSelectorShadow: ".orgchart-splitter-shadow-left",
        //    resizeHeight: false
        // });
        // $(".orgchart-panel-grid").resizable_left_splitter({
        //    handleSelector: ".orgchart-splitter-right",
        //    handleSelectorShadow: ".orgchart-splitter-shadow-right",
        //    resizeHeight: false
        // });
        // $(".orgchart-panel-top").resizable_left_splitter({
        //    handleSelector: ".orgchart-splitter-horizontal",
        //    resizeWidth: false
        // });
        $(".orgchart-panel-left").resizable({
           handleSelector: ".orgchart-splitter-left",
           handleSelectorShadow: ".orgchart-splitter-shadow-left",
           resizeHeight: false
         });

         $(".orgchart-panel-top").resizable({
           handleSelector: ".orgchart-splitter-horizontal",
           resizeWidth: false
         });
      }

      // hide search option
      $('#toggle_search_option').hide();
    }

    if (OrgConfig.appType === 'DEPTUSER' || OrgConfig.appType === 'USER') {
      if (OrgConfig.onlyOneSelect === true) {
        var treeWidth = $('#pnl_content_tree').css('width');
        $('#pnl_content_grid').css('width', `calc(100% - ${treeWidth + 5}px)`);

        // &
        require('./resize-extend');
        // $(".orgchart-panel-left").css('width', '240').css('min-width', '100');
        // $(".orgchart-panel-grid").css('width', '200').css('min-width', '50');
        // $(".orgchart-panel-right").css('width', '0').css('min-width', '0').hide();
        // $(".orgchart-splitter-right").css("width", '0').css("min-width", '0').hide();

        $(".orgchart-panel-left").resizable({
          handleSelector: ".orgchart-splitter-left",
          handleSelectorShadow: ".orgchart-splitter-shadow-left",
          resizeHeight: false
        });

        $(".orgchart-panel-top").resizable({
          handleSelector: ".orgchart-splitter-horizontal",
          resizeWidth: false
        });
      }
      else {
        $('.org_window').css('min-width', '765px');

        // 3
        require('./resize-extend');
        $(".orgchart-panel-left").resizable({
         handleSelector: ".orgchart-splitter-left",
         handleSelectorShadow: ".orgchart-splitter-shadow-left",
         resizeHeight: false
        });

        $(".orgchart-panel-grid").resizable({
         handleSelector: ".orgchart-splitter-right",
         handleSelectorShadow: ".orgchart-splitter-shadow-right",
         resizeHeight: false
        });

        $(".orgchart-panel-top").resizable({
         handleSelector: ".orgchart-splitter-horizontal",
         resizeWidth: false
        });

        //require('./resize-extend');
        // $(".orgchart-panel-left").resizable({
        //   handleSelector: ".orgchart-splitter-left",
        //   handleSelectorShadow: ".orgchart-splitter-shadow-left",
        //   resizeHeight: false
        // });
        // $(".orgchart-panel-grid").resizable({
        //   handleSelector: ".orgchart-splitter-right",
        //   handleSelectorShadow: ".orgchart-splitter-shadow-right",
        //   resizeHeight: false
        // });
        // $(".orgchart-panel-top").resizable({
        //   handleSelector: ".orgchart-splitter-horizontal",
        //   resizeWidth: false
        // });


        // 2016-05-30 daddygom, Split Panel TEST CODE *****************


        // -- 1. 3 panel    ********************************
        //  $(".orgchart-panel-left").resizable({
        //    handleSelector: ".orgchart-splitter-left",
        //    handleSelectorShadow: ".orgchart-splitter-shadow-left",
        //    resizeHeight: false
        //  });

        //  $(".orgchart-panel-grid").resizable({
        //    handleSelector: ".orgchart-splitter-right",
        //    handleSelectorShadow: ".orgchart-splitter-shadow-right",
        //    resizeHeight: false
        //  });

        //  $(".orgchart-panel-top").resizable({
        //    handleSelector: ".orgchart-splitter-horizontal",
        //    resizeWidth: false
        //  });

        // -- 2. 2 Panel grid , right hide   *****************
        // $(".orgchart-panel-left").css('width', '240').css('min-width', '100');
        // $(".orgchart-panel-grid").css('width', '200').css('min-width', '50');
        // $(".orgchart-panel-right").css('width', '0').css('min-width', '0').hide();
        // $(".orgchart-splitter-right").css("width", '0').css("min-width", '0').hide();

        // $(".orgchart-panel-left").resizable({
        //   handleSelector: ".orgchart-splitter-left",
        //   handleSelectorShadow: ".orgchart-splitter-shadow-left",
        //   resizeHeight: false
        // });

        // $(".orgchart-panel-top").resizable({
        //   handleSelector: ".orgchart-splitter-horizontal",
        //   resizeWidth: false
        // });

        // -- 2. 3 right panel , grid hide   *****************
        // $(".orgchart-panel-left").css("width", '240').css('min-width', '100');
        // $(".orgchart-panel-right").css("width", '200').css("min-width", '50');
        // $(".orgchart-panel-grid").css("width", '0').css("min-width", '0').hide();
        // $(".orgchart-splitter-right").css("width", '0').css("min-width", '0').hide();

        // $(".orgchart-panel-left").resizable({
        //    handleSelector: ".orgchart-splitter-left",
        //    handleSelectorShadow: ".orgchart-splitter-shadow-left",
        //    resizeHeight: false
        //  });
        //
        //  $(".orgchart-panel-top").resizable({
        //    handleSelector: ".orgchart-splitter-horizontal",
        //    resizeWidth: false
        //  });

        // *************************************************************
      }
    }
    else if (OrgConfig.appType === 'DEPT') {
      if (OrgConfig.onlyOneSelect === true) {
        // 1
        $('#resizer_tree').css('width', '0').hide();
        $('#pnl_content_tree').css('width', '100%');

        // hide search
        $('#toggle_search_option').hide();
        $('#search-box').hide();
      }
    }
  }
  else if (OrgConfig.section === 'MAIL') {
    $('.org_window').css('min-width', '765px');

    require('./resize-extend');
    $(".orgchart-panel-left").resizable({
      handleSelector: ".orgchart-splitter-left",
      handleSelectorShadow: ".orgchart-splitter-shadow-left",
      resizeHeight: false
    });
    $(".orgchart-panel-grid").resizable({
      handleSelector: ".orgchart-splitter-right",
      handleSelectorShadow: ".orgchart-splitter-shadow-right",
      resizeHeight: false
    });
    $(".orgchart-panel-top").resizable({
      handleSelector: ".orgchart-splitter-horizontal",
      resizeWidth: false
    });

    $('#pnl-list-bcc .list_footer').css('border-bottom', '0');
  }


  var PresenceManager = require('./presence').default;
  PresenceManager.initialize();

  // for lazy import
  var AppManager = require('./app-manager').default;
  var DNDExtend = require('./dnd-extend').default;
  var TooltipExtend = require('./tooltip-extend').default;
  var SelectExtend = require('./select-extend').default;
  var DeptSearch = require('./dept-search').default;
  var AjaxHelper = require('./ajax-helper').default;
  var PopupExtend = require('./popup-extend').default;

  Org.app = AppManager;
  Org.component.spinner = require('./spinner-extend').default;

  Org.component.spinner.show();

  AjaxHelper.ajaxUserInfo(function(j) {
    // AppConfig.info
    var settings = j.Settings;
    var companies = j.Companies;

    window.AppConfig.info.Settings = settings;
    window.AppConfig.info.Companies = companies;
    if (OrgConfig.selectDeptCode != '') {
      window.AppConfig.info.Settings.DeptCode = OrgConfig.selectDeptCode;
    }

    // set my info
    AppManager.userInfo.companyCode = settings.CompanyCode;
    AppManager.userInfo.childCompanyCode = settings.ChildCompanyCode;
    AppManager.userInfo.deptCode = settings.DeptCode;
    AppManager.userInfo.emailAddress = settings.EmailAddress;

    Org.component.spinner.hide();

    Org.invoke('load');
    AppManager.initialize();
    Org.controls = AppManager.controls;

    if (AppManager.multiLang.length > 1) {
      $('#btn-language-slide').click(function(e) {
        var ul = $('#ul-language-slide');
        if (ul.css('display') === 'none') {
          ul.slideDown(200);
        }
        else {
          ul.slideUp(200);
        }
      });
      $(document).click(function(e) {
        if (e.target.id !== 'btn-language-slide') {
          $('#ul-language-slide').slideUp(200);
        }
      });
    }
  }.bind(this), function(err) {
    Org.component.spinner.hide(false);

    var html = '    .<br />   .<br />';
    PopupExtend.show('Error', html);
  })

  DNDExtend.initialize();
  TooltipExtend.initialize();
  DeptSearch.initialize();


  var useDblClickTo = false;
  if (OrgConfig.onlyOneSelect !== true) {
    useDblClickTo = true;
  }
  else if (OrgConfig.section === 'APP' && OrgConfig.appType !== 'DEPTUSER' && OrgConfig.appType !== 'USER') {
    useDblClickTo = true;
  }

  if (useDblClickTo === true) {
    $('#orgGrid')
    .on('dblclick', 'tr', function(e) {
      if (!e.ctrlKey && !e.shiftKey) {
        var items = [];
        
        if (OrgConfig.oneClickToggle === false) {
          items = AppManager.controls.grid.getCheckedItems();
        }
        else {
          var uid = $(this).attr('data-unique');
          var uItem = AppManager.controls.grid.getItemByUnique(uid);
          if (uItem !== null && uItem.checked) {
            items = [uItem];
          }
        }

        for (var i = 0; i < items.length; i++) {
          var unique = items[i].data.UserID;
          if (typeof unique === 'undefined' || unique === '') {
            unique = items[i].data.EmailAddress;
          }
          if (AppManager.controls.to !== null) {
            AppManager.controls.to.addItem({
              //unique: items[i].data.EmailAddress,
              unique: unique,
              data: items[i].data,
              columns: [
                items[i].data.DisplayName,
              ]
            });
          }
          else if (AppManager.controls.selectList !== null) {
            AppManager.controls.selectList.addItem({
              //unique: items[i].data.EmailAddress,
              unique: unique,
              data: items[i].data,
              columns: [
                items[i].data.DisplayName,
              ]
            });
          }
        }
      }
    });
  }

  if (OrgConfig.deptSelection && OrgConfig.appType !== 'USER') {
    $('#orgTree')
    .on('dblclick', 'a', function(e) {
      if (!e.ctrlKey && !e.shiftKey) {
        var item = AppManager.controls.tree.getSelectedItem();
		
		if (item === null) {
			return false;
		}
		else if (!_.has(item.data, 'EmailAddress')) {
			return false;
		}
		else if (item.data.EmailAddress === null || item.data.EmailAddress === '') {
			return false;
		}
		
		if (item.data.DisableDND) {
			return false;
		}
	
        var unique = item.data.UserID;
        if (typeof unique === 'undefined' || unique === '') {
          unique = item.data.EmailAddress;
        }

        if (AppManager.controls.to !== null) {
          AppManager.controls.to.addItem({
            unique: unique,
            data: item.data,
            columns: [
              item.data.DisplayName
            ]
          });
        }
        else if(AppManager.controls.selectList !== null) {
          AppManager.controls.selectList.addItem({
            unique: unique,
            data: item.data,
            columns: [
              item.data.DisplayName
            ]
          });
        }
      }
    });
  }

});



