import { tooltipTimer, resetTooltip } from './tooltip-extend';
import SpinnerExtend from './spinner-extend';
import PopupExtend from './popup-extend';
import AjaxHelper from './ajax-helper';
var logger = require('loglevel');

var $ = require('jquery');

var xhr = null;
function ajaxAbort() {
  if (xhr !== null) {
    xhr.abort();
    xhr = null;
  }
}
function ajaxSearchDept(companyCode, keyword, langCode, fn, fe) {
  ajaxAbort();

  xhr = $.ajax({
    type: 'GET',
    dataType: 'json',
    url: `Rest.aspx?action=GetSearchDept&companyCode=${companyCode}&keyword=${encodeURIComponent(keyword)}&langCode=${langCode}`,
  })
  .done(fn)
  .fail(fe);
}
function ajaxSearchDeptList() {
  var companyCode = AppManager.companyCode;
  var langCode = AppManager.language;

  ajaxSearchDept(companyCode, context.keyword, langCode, function(j) {
    xhr = null;

    results = j.Items;

    if (j.Items.length > 0) {
      var html = '';
      for (var i = 0; i < j.Items.length; i++) {
        html += `<li data-code='${j.Items[i].DeptCode}' unselectable="on">${j.Items[i].DisplayName}</li>`;
      }
      selectResult.innerHTML = html;
      if (selectResult.style.display === 'none' || selectResult.style.display === '') {
        $(selectResult).slideDown(50);
      }
    }
    else {
      selectResult.style.display = 'none';
    }
  }, function() {
    // if fail, notthing to do (or clear?)
    xhr = null;
  });
}

function doDeptSearch() {
  $('#btn-dept-search-cancel').show();

  var val = deptSearchInput.value;
  var companyCode = AppManager.companyCode;
  var langCode = AppManager.language;

  if (context.timer !== null) {
    clearTimeout(context.timer);
    context.timer = null;
    ajaxAbort();
  }
  selectResult.style.display = 'none';

  resetTooltip();
  SpinnerExtend.show();

  ajaxSearchDept(companyCode, val, langCode, function(j) {
    xhr = null;

    AppManager.controls.tree.clear();
    if (j.Items.length > 0) {
      for (var i = 0; i < j.Items.length; i++) {
        AppManager.controls.tree.addNode({
          unique: j.Items[i].DeptCode,
          text: j.Items[i].DeptName,
          hasChild: false,
          expanded: true,
          selected: i === 0,
          data: j.Items[i],
        });
      }
    }
    else {
      AppManager.setPaging(1, 1);
      AppManager.setDefaultHeader();
      AppManager.controls.grid.clear();
    }

    SpinnerExtend.hide();
  }, function() {
    // if fail, notthing to do (or clear?)
    xhr = null;
    SpinnerExtend.hide(false);
  });
}

Element.prototype.documentOffsetTop = function () {
    return this.offsetTop + ( this.offsetParent ? this.offsetParent.documentOffsetTop() : 0 );
};

