var deptSearchInput = null;
var selectResult = null;
var context = {
  keyword: '',
  timer: null,
  prevState: null,
};
var results = [];

// jquery events
function initJqueryEvents() {
  $('#btn-dept-search').click(function() {
    //doDeptSearch();
    var val = deptSearchInput.value;
    if (val === '') {
      if (context.timer !== null) {
        clearTimeout(context.timer);
        context.timer = null;
      }
      ajaxAbort();
      selectResult.style.display = 'none';
      context.keyword = '';
      return;
    }

    if (context.timer !== null) {
      clearTimeout(context.timer);
      context.timer = null;
      ajaxAbort();
    }

    context.keyword = val;
    resetTooltip();
    ajaxSearchDeptList();
  });

  $(selectResult)
  .on({
    mouseenter: function(e) {
      var $selected = $(this).siblings('li[data-selected]');
      if ($selected.length > 0) {
        $selected[0].removeAttribute('data-selected');
      }

      this.setAttribute('data-selected', '');
    },
    mouseleave: function(e) {
      this.removeAttribute('data-selected');
    }
  }, 'li')
  .on('click', 'li', function(e) {
    var code = this.getAttribute('data-code');
    selectSearchNode(code);
    reset();
  })

  // keyboard nav
  $(deptSearchInput)
  .on('click change focus', function(e) {
    var $selectResult = $(selectResult);
    var $selected = $selectResult.children('li[data-selected]');
    if ($selected.length > 0) {
      $selected[0].removeAttribute('data-selected');
    }
  })
  .keydown(function(e) {
    var el = null;

    // up
    if (e.keyCode === 40) {
      if (selectResult.style.display !== 'block') {
        if (results.length > 0) {
          selectResult.style.display = 'block';
        }
      }

      if (selectResult.style.display === 'block') {
        e.preventDefault();
        resetTooltip();

        var $selectResult = $(selectResult);
        var $selected = $selectResult.children('li[data-selected]');
        if ($selected.length > 0) {
          $selected[0].removeAttribute('data-selected');
        }

        if ($selected.next().length === 0) {
          el = $selectResult.children().first()[0];
        }
        else {
          el = $selected.next()[0];
        }

        if (el !== null) {
          el.setAttribute('data-selected', '');
          var offset = $(el).offset();
          var x = offset.left + el.clientWidth + 2;
          var y = offset.top;
          tooltipTimer(getItem(el.getAttribute('data-code')), x, y);
        }
      }
    }
    // down
    else if (e.keyCode === 38) {
      if (selectResult.style.display !== 'block') {
        if (results.length > 0) {
          selectResult.style.display = 'block';
        }
      }

      if (selectResult.style.display === 'block') {
        e.preventDefault();
        resetTooltip();

        var $selectResult = $(selectResult);
        var $selected = $selectResult.children('li[data-selected]');
        if ($selected.length > 0) {
          $selected[0].removeAttribute('data-selected');
        }

        if ($selected.prev().length === 0) {
          el = $selectResult.children().last()[0];
        }
        else {
          el = $selected.prev()[0];
        }

        if (el !== null) {
          el.setAttribute('data-selected', '');
          var offset = $(el).offset();
          var x = offset.left + el.clientWidth + 2;
          var y = offset.top;
          tooltipTimer(getItem(el.getAttribute('data-code')), x, y);
        }
      }
    }
    // enter
    else if (e.keyCode === 13) {
      if (selectResult.style.display === 'block') {
        var $selectResult = $(selectResult);
        var $selected = $selectResult.children('li[data-selected]');
        if ($selected.length > 0) {
          e.preventDefault();
          resetTooltip();

          var code = $selected[0].getAttribute('data-code');
          selectSearchNode(code);
        }
      }
      else {
        var val = deptSearchInput.value;
        if (val === '') {
          if (context.timer !== null) {
            clearTimeout(context.timer);
            context.timer = null;
          }
          ajaxAbort();
          selectResult.style.display = 'none';
          context.keyword = '';
          return;
        }

        if (context.timer !== null) {
          clearTimeout(context.timer);
          context.timer = null;
          ajaxAbort();
        }

        context.keyword = val;
        resetTooltip();
        ajaxSearchDeptList();
      }

      // if (val === '') {
      //   if ($('#btn-dept-search-cancel').is(':visible')) {
      //     AppManager.setUserDeptTree();
      //     reset();
      //     resetTooltip();
      //     return;
      //   }
      // }
      //
      // if (selectResult.style.display === 'block') {
      //   var $selectResult = $(selectResult);
      //   var $selected = $selectResult.children('li[data-selected]');
      //   if ($selected.length > 0) {
      //     e.preventDefault();
      //     resetTooltip();
      //
      //     var code = $selected[0].getAttribute('data-code');
      //     var node = AppManager.controls.tree.setSelectedByUnique(code);
      //     reset();
      //
      //     if (node === null) {
      //       //       , Ajax  .
      //       AppManager.setUserDeptTree(code);
      //     }
      //
      //     return;
      //   }
      // }
      //
      // //        
      // e.preventDefault();
      // doDeptSearch();
    }
    // esc
    else if (e.keyCode === 27) {
      var $cancel = $('#btn-dept-search-cancel');
      if ($cancel.is(':visible')) {
        //  
        AppManager.setUserDeptTree();
        reset();
        resetTooltip();
      }
      else {
        //  list 
        $('#select-dept-search').hide();
        resetTooltip();
      }
    }
  })
  .on('change paste keyup', function() {
    var val = deptSearchInput.value;

    if (val === '') {
      if (context.timer !== null) {
        clearTimeout(context.timer);
        context.timer = null;
      }
      ajaxAbort();
      selectResult.style.display = 'none';
      context.keyword = '';
      return;
    }

    if (val === context.keyword) {
      return;
    }

    if (context.timer !== null) {
      clearTimeout(context.timer);
      context.timer = null;
      ajaxAbort();
    }

    var keyword = val;
    context.keyword = val;
    context.timer = setTimeout(() => {
      ajaxSearchDeptList();
      // var companyCode = AppManager.companyCode;
      // var langCode = AppManager.language;
      //
      // ajaxSearchDept(companyCode, context.keyword, langCode, function(j) {
      //   xhr = null;
      //
      //   results = j.Items;
      //
      //   if (j.Items.length > 0) {
      //     var html = '';
      //     for (var i = 0; i < j.Items.length; i++) {
      //       html += `<li data-code='${j.Items[i].DeptCode}' unselectable="on">${j.Items[i].DisplayName}</li>`;
      //     }
      //     selectResult.innerHTML = html;
      //     if (selectResult.style.display === 'none' || selectResult.style.display === '') {
      //       $(selectResult).slideDown(50);
      //     }
      //   }
      //   else {
      //     selectResult.style.display = 'none';
      //   }
      // }, function() {
      //   // if fail, notthing to do (or clear?)
      //   xhr = null;
      // });
    }, 600);
  });

  $('#btn-dept-search-cancel').click(function() {
    AppManager.setUserDeptTree();
    reset();
  });
}

function initialize() {
  deptSearchInput = document.getElementById('input-dept-search');
  selectResult = document.getElementById('select-dept-search');

  initJqueryEvents();

  // AppManager.addTreeSelectExtend(function() {
  //   if (AppManager.viewMode === 'search-dept') {
  //     AppManager.fetchDeptGridData.call(AppManager);
  //   }
  // });
}

function getItem(code) {
  for (var i = 0; i < results.length; i++) {
    if (results[i].DeptCode === code) {
      return results[i];
    }
  }
  return null;
}

function reset() {
  deptSearchInput.value = '';
  context.keyword = '';
  if (context.timer !== null) {
    clearTimeout(context.timer);
    context.timer = null;
    ajaxAbort();
  }
  selectResult.style.display = 'none';
  $('#btn-dept-search-cancel').hide();
}

export { reset }

export default {
  initialize: initialize,
  getItem: getItem,
}



