var popupTitle = document.querySelector('#popup-title');
var popupText = document.querySelector('#popup-txt');

var $popup = $('#popup-container');

$popup.find('.btn_close').unbind("click").bind("click",function(){
    $('.pop_wrap').fadeOut(200);
    $popup.fadeOut(200);
});

function fn_popup_set() {
  $popup.css('margin-top', - $popup.outerHeight()/2 );
  if ( $popup.hasClass('pop_layer') ){
    if ($popup.find('.pop_footer').length > 0){
      $popup.find('.pop_body').css('height', $popup.outerHeight()-$popup.find('.pop_header').outerHeight() - $popup.find('.pop_footer').outerHeight() );
    } else {
      $popup.find('.pop_body').css('height', $popup.outerHeight()-$popup.find('.pop_header').outerHeight() );
    }
  };
}

$(window).resize(function(){
  fn_popup_set();
});

export default {
  show: function(title, html) {
    popupTitle.innerText = title;
    popupText.innerHTML = html;

    var option = {
      duration: 200,
      step: function() {
        fn_popup_set();
      }
    };
    $('.pop_wrap > .bg').css('background-color', 'rgba(0, 0, 0, .8)');
    $('.pop_wrap').fadeIn(option);
    $popup.fadeIn(200);

    // $('.bg').click(function(){
    //   $('.pop_wrap').fadeOut(200);
    //   $popup.fadeOut(200);
    // });
  }
};



